import axios from 'axios';

const PrioritisationService = {
  savePriotisation(json) {
    const url = `${process.env.REACT_APP_HOST_URL}Title/SavePrioritisation`;
    return axios.post(url, json);
  },

  getChannels() {
    const url = `${process.env.REACT_APP_HOST_URL}Channel/GetChannelLookup`;
    return axios.get(url);
  },

  getTitles(querystring, filter, prioritisedPrograms, fromSearch) {
    // prettier-ignore
    const prioritisedProgramsList = prioritisedPrograms === null ? [] : prioritisedPrograms;
    const url = `${process.env.REACT_APP_HOST_URL}Title/GetProgrammeTitlesList${querystring}&channelId=${filter.channelId}&month=${filter.month}&year=${filter.year}&fromSearch=${fromSearch}`;
    // +
    // '&titleIds=' +
    // filter.titleIds
    return axios.post(url, prioritisedProgramsList);
  },

  getPriorityList(parameters) {
    const url = `${process.env.REACT_APP_HOST_URL}Title/GetPrioritisationList?channelId=${parameters.channelId}&month=${parameters.month}&year=${parameters.year}`;
    return axios.get(url);
  },

  getTitleByID(id, channelId) {
    const url = `${process.env.REACT_APP_HOST_URL}Title/GetTitleDetails?titleId=${id}&channelId=${channelId}`;
    return axios.get(url);
  },

  uploadFile(formData, channelId, year, month, fileExtension) {
    const url = `${process.env.REACT_APP_HOST_URL}Title/ImportExcelFile?channelId=${channelId}&year=${year}&month=${month}&fileExtension=${fileExtension}`;
    return axios.post(url, formData);
  },

  getSegments(id) {
    const url = `${process.env.REACT_APP_HOST_URL}Title/GetSegments/${id}`;
    return axios.get(url);
  },

  exportUploadSample() {
    const url = `${process.env.REACT_APP_HOST_URL}Title/ExportTitleUploader`;
    return axios.get(url, { responseType: 'arraybuffer' });
  },

  generateSegments(parameters) {
    const url = `${process.env.REACT_APP_HOST_URL}Title/GenerateSegments?ChannelId=${parameters.channelId}&Month=${parameters.month}&Year=${parameters.year}`;
    return axios.get(url);
  },

  getPrioritisationSession() {
    const url = `${process.env.REACT_APP_HOST_URL}Channel/GetPrioritisationSession`;
    return axios.get(url);
  },

  savePrioritisationSession(json) {
    const url = `${process.env.REACT_APP_HOST_URL}Channel/AddPrioritisationSession${json}`;
    return axios.post(url, json);
  },
  getTitleGroupList(parameters, initial) {
    const url = `${process.env.REACT_APP_HOST_URL}TitleGroup/GetTitlesGroupList${parameters}&initial=${initial}`;
    return axios.get(url);
  },
  getTitleGroupPrograms(id, channelId, start, end) {
    const url = `${process.env.REACT_APP_HOST_URL}Schedule/GetTitleGroupPrograms?searchKey=${id}&channelId=${channelId}&start=${start}&end=${end}`;
    return axios.get(url);
  },
  saveTitleGroupPriotisation(json) {
    const url = `${process.env.REACT_APP_HOST_URL}TitleGroup/SaveTitleGroupPrioritisation`;
    return axios.post(url, json);
  },
};

export default PrioritisationService;
