import React, { useRef, forwardRef, useImperativeHandle } from 'react';
import PropTypes from 'prop-types';
import eyeIcon from '../../../assets/icons/Prioritisation-eye.svg';
import eyeIconSelected from '../../../assets/icons/Prioritisation-selectedeye.svg';
import eyeOffIcon from '../../../assets/icons/Eye.png';
import Styles from '../Prioritisation.module.css';
import BootstrapSpinner from '../../../shared/components/bootstrap-spinner/BootstrapSpinner';

const AvailableProgramsDataComponent = forwardRef(
  (
    {
      programs,
      callbackSelectPrograms,
      callbackViewProgram,
      isLoading,
      selectedPrograms,
      callbackViewSegment,
      tableMinimize,
      titleSource,
      viewedProgram,
    },
    ref
  ) => {
    const eyeRef = useRef();

    useImperativeHandle(ref, () => ({
      scrollToProgram() {
        setTimeout(() => {
          eyeRef.current?.scrollIntoView({
            behavior: 'smooth',
            block: 'center',
          });
        }, 500);
      },
    }));

    const selectRow = (data) => {
      let filteredArray = [];
      if (selectedPrograms.includes(data.id)) {
        filteredArray = selectedPrograms.filter((e) => e !== data.id);
        // setSelectedPrograms(filteredArray);
        callbackSelectPrograms(filteredArray);
      } else {
        filteredArray = selectedPrograms;
        filteredArray.push(data.id);
        //  setSelectedPrograms(filteredArray);
        filteredArray = selectedPrograms.filter((e) => e !== data.id);
        // setSelectedPrograms(filteredArray);
        filteredArray.push(data.id);
        //  setSelectedPrograms(filteredArray);
        callbackSelectPrograms(filteredArray);
      }
    };

    const viewRow = (data) => {
      callbackViewProgram(data.target.name);
    };

    const viewSegmentRow = (data) => {
      callbackViewSegment(data.target.name);
    };

    // const getGenre = (data) => {
    //   const genreList = [];
    //   data.genres.map((genreData) => {
    //     genreList.push(genreData.genreName);
    //     return true;
    //   });
    //   // genreText = genreList.join(', ');
    // };

    const eyeIconSource = (data) => {
      if (viewedProgram && parseInt(viewedProgram, 10) === data.id)
        return eyeOffIcon;
      if (selectedPrograms.includes(data.id)) return eyeIconSelected;
      return eyeIcon;
    };

    const filterStyle = (data) => {
      if (selectedPrograms.includes(data.id))
        return 'brightness(0) saturate(100%) invert(100%) sepia(100%) saturate(0%) hue-rotate(264deg) brightness(105%) contrast(104%)';
      if (
        parseInt(viewedProgram, 10) === data.id &&
        selectedPrograms.includes(data.id)
      )
        return 'brightness(0) saturate(100%) invert(100%) sepia(100%) saturate(0%) hue-rotate(264deg) brightness(105%) contrast(104%)';
      return 'brightness(0) saturate(100%) invert(23%) sepia(67%) saturate(6736%) hue-rotate(162deg) brightness(100%) contrast(101%)';
    };

    const noProgramsData = () => {
      if (isLoading) {
        return (
          <div
            className={`${
              !tableMinimize ? Styles.tableBodyDiv : Styles.tableBodyDivSmall
            }`}
            style={{ backgroundColor: 'transparent' }}
          >
            <BootstrapSpinner />
          </div>
        );
      }
      return (
        <tr
          className={`${
            !tableMinimize ? Styles.tableBodyDiv : Styles.tableBodyDivSmall
          }`}
        >
          <td
            colSpan="7"
            style={{
              fontSize: '24px',
              fontWeight: '300',
              color: '#008662',
              textAlign: 'center',
              backgroundColor: 'transparent',
            }}
          >
            No Programme Titles Found
          </td>
        </tr>
      );
    };

    const getColumns = (data) => (
      <tr
        className={`
          ${
            selectedPrograms.includes(data.id)
              ? 'table-body-row-active'
              : 'table-body-row'
          }
          ${data.overrode && Styles.isHighlighted}
        `}
      >
        <td
          className="table-body-column"
          style={{ textIndent: '25px' }}
          onClick={() => selectRow(data)}
          onKeyDown={() => selectRow(data)}
          role="presentation"
          data-toggle="tooltip"
          data-placement="top"
          title={data.title}
        >
          {data.title}
        </td>
        {titleSource === 1 && (
          <td
            className="table-body-column"
            style={{ textIndent: '30px' }}
            onClick={() => selectRow(data)}
            onKeyDown={() => selectRow(data)}
            role="presentation"
            data-toggle="tooltip"
            data-placement="top"
            title={data.genreString}
          >
            {data.genreString}
          </td>
        )}
        <td
          className="table-body-column"
          style={{
            textIndent: titleSource === 2 ? '25px' : '33px',
          }}
          onClick={() => selectRow(data)}
          onKeyDown={() => selectRow(data)}
          role="presentation"
          data-toggle="tooltip"
          data-placement="top"
          title={data.duration}
        >
          {data.duration}
        </td>
        {titleSource === 1 && (
          <td
            className="table-body-column"
            onClick={() => selectRow(data)}
            onKeyDown={() => selectRow(data)}
            role="presentation"
            data-toggle="tooltip"
            data-placement="top"
            style={{ textIndent: '35px' }}
            title={data.grade}
          >
            {data.grade}
          </td>
        )}
        {titleSource === 1 && (
          <td
            className="table-body-column"
            onClick={() => selectRow(data)}
            onKeyDown={() => selectRow(data)}
            role="presentation"
            data-toggle="tooltip"
            data-placement="top"
            style={{ textIndent: '35px' }}
            title={data.certificate}
          >
            {data.certificate}
          </td>
        )}
        {titleSource === 1 && (
          <td
            className="table-body-column"
            style={{ textIndent: '40px' }}
            onClick={() => selectRow(data)}
            onKeyDown={() => selectRow(data)}
            role="presentation"
            data-toggle="tooltip"
            data-placement="top"
            title={data.year}
          >
            {data.year}
          </td>
        )}
        {titleSource === 1 && (
          <td className="table-body-column" style={{ width: '30px' }}>
            <img
              ref={
                viewedProgram && parseInt(viewedProgram, 10) === data.id
                  ? eyeRef
                  : null
              }
              name={data.id}
              style={{
                cursor: 'pointer',
                marginLeft: '-20px',
                filter: filterStyle(data),
              }}
              src={eyeIconSource(data)}
              alt="EyeIcon"
              onClick={viewRow}
              onKeyDown={viewRow}
              role="presentation"
            />
          </td>
        )}
        {titleSource === 2 && (
          <td className="table-body-column" style={{ width: '130px', }}>
            <img
              ref={
                viewedProgram && parseInt(viewedProgram, 10) === data.id
                  ? eyeRef
                  : null
              }
              name={data.id}
              style={{
                cursor: 'pointer',
                filter: filterStyle(data),
              }}
              src={eyeIconSource(data)}
              alt="EyeIcon"
              onClick={viewRow}
              onKeyDown={viewRow}
              role="presentation"
            />
          </td>
        )}
      </tr>
    );

    const availablePrograms = () => {
      if (isLoading) {
        return (
          <div
            className={`${
              !tableMinimize ? Styles.tableBodyDiv : Styles.tableBodyDivSmall
            }`}
            style={{ backgroundColor: 'transparent' }}
          >
            <BootstrapSpinner />
          </div>
        );
      }
      return <>{programs.map((data) => getColumns(data))}</>;
    };

    return (
      <>
        {programs.length === 0 ? noProgramsData() : availablePrograms()}
        {/* {programs.length > 0 && availablePrograms()} */}
      </>
    );
  }
);
AvailableProgramsDataComponent.propTypes = {
  programs: PropTypes.arrayOf(PropTypes.shape),
  callbackSelectPrograms: PropTypes.func.isRequired,
  callbackViewProgram: PropTypes.func.isRequired,
  isLoading: PropTypes.bool,
  selectedPrograms: PropTypes.arrayOf(PropTypes.number),
  callbackViewSegment: PropTypes.func.isRequired,
  tableMinimize: PropTypes.bool,
  titleSource: PropTypes.number.isRequired,
  viewedProgram: PropTypes.number,
};
AvailableProgramsDataComponent.defaultProps = {
  programs: null,
  isLoading: false,
  selectedPrograms: null,
  tableMinimize: false,
  viewedProgram: null,
};
AvailableProgramsDataComponent.displayName = 'AvailableProgramsDataComponent';
export default AvailableProgramsDataComponent;
