import React, { useState, useEffect, useRef } from 'react';
import { Modal } from 'react-bootstrap';
import { saveAs } from 'file-saver';
import Styles from './Prioritisation.module.css';
import xIcon from '../../assets/icons/X.svg';
import checkIcon from '../../assets/icons/Check.svg';
import { arrowLeftIcon, arrowRightIcon } from '../../assets/index';
import BannerComponent from '../_reusable/banner/BannerComponent';
import SelectComponent from '../../shared/elements/select/select.component';
import SelectComponentCustom from '../../shared/elements/select/select.componentcustom';
import AvailableProgramsComponent from './available-programs/AvailableProgramsComponent';
import PrioritisedProgramsComponent from './prioritised-programs/PrioritisedProgramsComponent';
import { useCallbackPrompt } from '../_reusable/blocker/UsePromptComponent.ts';
import PrioritisationService from './PrioritisationService';
import ProfileService from '../profile/ProfileService';
import AuthService from '../../core/auth.service';
import PrioritisationForms from './PrioritisationForms';
import Button from '../../shared/components/button/Button';
import {
  success,
  danger,
} from '../../shared/elements/notification/NotifyComponent';
import ProgramDetailsComponent from './prioritisation-program-details/PrioritisationProgramDetailComponent';
import ImportProgramDetailsComponent from './import-program-details/ImportProgramDetailsComponent';
import ConfirmLeavePageModalComponent from '../title-group/modals/confirm-leave-page-modal/ConfirmLeavePageModalComponent';
import TabsComponent from '../_reusable/tabs/TabsComponent';
import TitleGroupComponent from './title-groups/TitleGroupComponent';

function PrioritisationComponent() {
  const [selectedChannel, setSelectedChannel] = useState(
    'Please select a channel'
  );
  const [channelOptions, setChannelOptions] = useState([{}]);

  const [selectedMonth, setSelectedMonth] = useState('January');
  const monthOptions = PrioritisationForms.months;
  const yearOptions = PrioritisationForms.year();

  const [selectedYear, setSelectedYear] = useState('2022');
  const [allprograms, setAllPrograms] = useState([]);
  const [programs, setPrograms] = useState([]);
  const [allPrioritisePrograms, setAllPrioritisePrograms] = useState([]);
  const [prioritisedPrograms, setPrioritisedPrograms] = useState([]);

  const [viewProgram, setViewProgram] = useState();

  const [selectedProgramID, setSelectedProgramID] = useState([]);
  const [selectedPriorityProgramID, setSelectedPriorityProgramID] = useState(
    []
  );
  const [isLoadingPrograms, setIsLoadingPrograms] = useState(true);
  const [isLoadingPrioritise, setIsLoadingPrioritise] = useState(false);
  const [hasSelectedPrograms, setHasSelectedPrograms] = useState(false);
  const [hasSelectedPrioritise, setHasSelectedPrioritise] = useState(false);
  const [clearSearchIsActive, setClearSearchIsActive] = useState(false);
  const [searchKey, setSearchKey] = useState('');
  const [filterModel, setFilterModel] = useState({});
  const [prioritisedSort, setPrioritisedSort] = useState(1);
  const [viewedProgram, setViewedProgram] = useState(null);
  const [saveSearchKey, setSaveSearchKey] = useState('');
  const [isSaving, setIsSaving] = useState(false);
  const [
    availablePaginationParameters,
    setAvailablePaginationParameters,
  ] = useState({});

  const [availableParameters, setAvailableParameters] = useState({
    page: 1,
    size: 50,
  });

  const [
    prioritisedPaginationParameters,
    setPrioritisedPaginationParameters,
  ] = useState({});
  const [prioritisedParameters, setPrioritisedParameters] = useState({
    page: 1,
    size: 50,
  });

  const [prioritisation, setPrioritisation] = useState({
    titleIds: [],
    channelId: null,
    month: 1,
    year: 2022,
  });
  const [sortBy, setSortBy] = useState([
    {},
    { state: false },
    { state: false },
    { state: false },
    { state: false },
    { state: true },
    { state: false },
  ]);

  const availableProgramsRef = useRef();
  const prioritisedProgramRef = useRef();
  const fileUploadRef = useRef();
  const [modalShow, setModalShow] = useState(false);
  const [titleSource, setTitleSource] = useState(1);
  const [hasTitles, setHasTitles] = useState(false);
  const [modalSuccessShow, setModalSuccessShow] = useState(false);
  const [modalVerifyShow, setModalVerifyShow] = useState(false);
  const [successCount, setSuccessCount] = useState('');
  const [fileNameUploaded, setFileNameUploaded] = useState('');
  const [uploadedTitleFilter, setUploadedTitleFilter] = useState('all');
  const [allUploadedTitles, setAllUploadedTitles] = useState([]);
  const [loadingUpload, setLoadingUpload] = useState(false);

  const [uploadedTitles, setUploadedTitles] = useState([]);
  const [uploadError, setUploadError] = useState('');
  const [segments, setSegments] = useState([]);
  const [tableMinimize, setTableMinimize] = useState(false);
  const [hasSegments, setHasSegments] = useState(false);
  const [modalLoadingShow, setModalLoadingShow] = useState(false);
  const [modalSegmentsShow, setModalSegmentsShow] = useState(false);
  const [modalVerifySegmentsShow, setModalVerifySegmentsShow] = useState(false);

  const [isDirty, setIsDirty] = useState(false);
  const [isTitleGroupDirty, setIsTitleGroupDirty] = useState(false);
  const [discardTitleGroupDirty, setDiscardTitleGroupDirty] = useState(false);
  const [saveTitleGroupDirty, setSaveTitleGroupDirty] = useState(false);
  const [nextTab, setNextTab] = useState();
  const [showConfirmLeaveModal, setShowConfirmLeaveModal] = useState(false);
  const [activeTab, setActiveTab] = useState('Titles');
  const [nextFilter, setNextFilter] = useState({});
  const [addedPrograms, setAddedPrograms] = useState([]);
  const [removedPrograms, setRemovedPrograms] = useState([]);
  const [
    showPrompt,
    confirmNavigation,
    cancelNavigation,
    hidePrompt,
  ] = useCallbackPrompt(isDirty || isTitleGroupDirty);
  const [renderTimes, setRenderTimes] = useState(0);
  const [
    prioritisationSessionParameters,
    setPrioritisationSessionParameters,
  ] = useState();

  const onChangeFormSetPrioritisationSessionParameters = (data) => {
    setPrioritisationSessionParameters({
      ...prioritisationSessionParameters,
      ...data,
    });
  };

  const cleanDirtyForm = () => {
    setIsDirty(false);
    setAddedPrograms([]);
    setRemovedPrograms([]);
  };

  const handleChangeTab = () => {
    if (nextTab) {
      setActiveTab(nextTab);
      setNextTab();
      setIsDirty(false);
      setIsTitleGroupDirty(false);
    }
  };

  const handleDiscardChanges = () => {
    if (isTitleGroupDirty) {
      setDiscardTitleGroupDirty(true);
    } else if (Object.keys(nextFilter).length > 0) {
      const { input } = nextFilter;
      const { selected } = nextFilter;
      cleanDirtyForm();
      setSearchKey('');
      setClearSearchIsActive(false);
      if (input === 'channel') {
        setSelectedChannel(selected.name);
        setPrioritisation((prevState) => ({
          ...prevState,
          channelId: selected.id,
        }));
        onChangeFormSetPrioritisationSessionParameters({
          channelID: selected.id,
        });
      } else if (input === 'month') {
        setSelectedMonth(selected.value);
        setPrioritisation((prevState) => ({
          ...prevState,
          month: selected.id,
        }));
        onChangeFormSetPrioritisationSessionParameters({
          monthID: selected.id,
        });
      } else {
        setSelectedYear(selected.id);
        setPrioritisation((prevState) => ({
          ...prevState,
          year: selected.id,
        }));
        onChangeFormSetPrioritisationSessionParameters({ year: selected.id });
      }
      setNextFilter({});
    }
    setShowConfirmLeaveModal(false);
    handleChangeTab();
  };

  const handleViewProgram = (programID) => {
    if (viewedProgram === programID) {
      setViewedProgram();
      setViewProgram();
      setTableMinimize(false);
      return false;
    }

    if (viewProgram !== undefined && viewProgram.titleId === programID) {
      setViewProgram();
      setViewedProgram();
      setTableMinimize(false);
    } else {
      PrioritisationService.getTitleByID(
        programID,
        prioritisation.channelId
      ).then((response) => {
        setViewedProgram(programID);
        availableProgramsRef.current.scrollToProgram();
        setViewProgram(response.data);
        setTableMinimize(true);
      });
    }
    return true;
  };

  const handleViewPriorityProgram = (programID) => {
    if (viewedProgram === programID) {
      setViewedProgram();
      setViewProgram();
      setTableMinimize(false);
      return false;
    }

    if (viewProgram !== undefined && viewProgram.titleId === programID) {
      setViewProgram();
      setViewedProgram();
      setTableMinimize(false);
    } else {
      PrioritisationService.getTitleByID(
        programID,
        prioritisation.channelId
      ).then((response) => {
        setViewProgram(response.data);
        setViewedProgram(programID);
        prioritisedProgramRef.current.scrollToProgram();
        setTableMinimize(true);
      });
    }
    return true;
  };

  const handleCloseDetails = () => {
    setViewProgram();
    setViewedProgram(null);
    setTableMinimize(false);
  };

  const constructQueryString = (filters, parameters, search) => {
    const searchKeyParam =
      search !== undefined && search !== '' ? `&SearchKey=${search}` : '';
    const titleFilterParam =
      filterModel.titleFilter !== undefined
        ? `&titleFilter=${filterModel.titleFilter}`
        : '';
    const titleFilterValueParam =
      filterModel.titleFilterValue !== undefined
        ? `&titleFilterValue=${filterModel.titleFilterValue}`
        : '';
    const genreFilterParam =
      filterModel.genreFilter !== undefined
        ? `&genreFilter=${filterModel.genreFilter}`
        : '';
    const genreFilterValueParam =
      filterModel.genreFilterValue !== undefined
        ? `&genreFilterValue=${filterModel.genreFilterValue}`
        : '';
    const durationFilterParam =
      filterModel.durationFilter !== undefined
        ? `&durationFilter=${filterModel.durationFilter}`
        : '';
    const durationFilterValueParam =
      filterModel.durationFilterValue !== undefined
        ? `&durationFilterValue=${filterModel.durationFilterValue}`
        : '';
    const gradeFilterParam =
      filterModel.gradeFilter !== undefined
        ? `&gradeFilter=${filterModel.gradeFilter}`
        : '';
    const gradeFilterValueParam =
      filterModel.gradeFilterValue !== undefined
        ? `&gradeFilterValue=${filterModel.gradeFilterValue}`
        : '';
    const yearFilterParam =
      filterModel.yearFilter !== undefined
        ? `&yearFilter=${filterModel.yearFilter}`
        : '';
    const yearFilterValueParam =
      filterModel.yearFilterValue !== undefined
        ? `&yearFilterValue=${filterModel.yearFilterValue}`
        : '';
    const sortByParam =
      filterModel.sortBy !== undefined
        ? `&SortOrder=${filterModel.sortBy}`
        : '';
    const sortParam =
      filterModel.userSort !== undefined
        ? `&ProgramSort=${filterModel.userSort}`
        : '';
    const certificate =
      filterModel.certificateFilter && filterModel.certificateFilterValue
        ? `&certificateFilter=${filterModel.certificateFilter}&certificateFilterValue=${filterModel.certificateFilterValue}`
        : '';

    let queryString =
      `?Page=${
        parameters.page === 0 || saveSearchKey !== search ? 1 : parameters.page
      }` +
      `&Size=${parameters.size}${searchKeyParam}${sortParam}${sortByParam}${titleFilterParam}${titleFilterValueParam}${genreFilterParam}${genreFilterValueParam}${durationFilterParam}${durationFilterValueParam}${gradeFilterParam}${gradeFilterValueParam}${yearFilterParam}${yearFilterValueParam}${certificate}`;

    if (queryString.includes('+')) {
      queryString = queryString.replace('+', '%2B');
    }

    return queryString;
  };

  const getPrioritisedPrograms = (programList) => {
    setIsLoadingPrioritise(true);
    let prioritisedProgramsList = programList;
    if (prioritisedSort === 1) {
      prioritisedProgramsList = prioritisedProgramsList.sort((a, b) => {
        if (a.title.toLowerCase() > b.title.toLowerCase()) return 1;
        return -1;
      });
    } else {
      prioritisedProgramsList = prioritisedProgramsList.sort((a, b) => {
        if (a.title.toLowerCase() > b.title.toLowerCase()) return -1;
        return 1;
      });
    }

    let pageNumber =
      prioritisedProgramsList.length > 0 && prioritisedParameters.page === 0
        ? 1
        : prioritisedParameters.page;
    const { size } = prioritisedParameters;
    let toSkip = pageNumber === 1 ? 0 : (pageNumber - 1) * size;

    while (toSkip >= prioritisedProgramsList.length) {
      pageNumber -= 1;
      toSkip = pageNumber === 1 ? 0 : (pageNumber - 1) * size;
    }
    const priotizePrograms = prioritisedProgramsList.slice(
      toSkip,
      toSkip + size
    );
    setPrioritisedPrograms(priotizePrograms);

    setPrioritisedParameters({ page: pageNumber, size });

    setPrioritisedPaginationParameters({
      totalCount: prioritisedProgramsList.length,
      pageSize: size,
      currentPage: pageNumber,
      totalPages: Math.ceil(prioritisedProgramsList.length / size),
    });

    setIsLoadingPrioritise(false);
  };

  const getPrioritisedTitles = (prioritisedProgramsList) => {
    const titleIds = prioritisedProgramsList.map((val) => val.id);
    return titleIds;
  };

  const constructProgrammeTitles = (data) => {
    const mappedData = data.map((item) => ({
      ...item,
      titleId: item.titleId,
      title: item.title,
      genreString: item.overrodeValues.genre || item.genreString,
      duration: item.overrodeValues.duration || item.duration,
      grade: item.overrodeValues.grade || item.grade,
      certificate: item.overrodeValues.certificate || item.certificate,
      year: item.overrodeValues.year || item.year,
    }));

    return mappedData;
  };

  const getPrograms = (
    filters,
    search,
    fromSearch,
    prioritiseList,
    fromFilter
  ) => {
    if (selectedChannel !== 'Please select a channel') {
      setIsLoadingPrograms(true);
      const newAvailableParameters = availableParameters;
      if (fromFilter) newAvailableParameters.page = 1;
      const query = constructQueryString(
        filters,
        newAvailableParameters,
        search
      );
      const titleList = getPrioritisedTitles(prioritiseList);
      PrioritisationService.getTitles(
        query,
        prioritisation,
        titleList,
        fromSearch
      ).then((response) => {
        setRenderTimes(renderTimes + 1);
        if (response.data.data.filter((o) => o.hasSegment).length === 0) {
          setHasSegments(false);
        } else {
          setHasSegments(true);
        }
        const constructedProgrammeTitles = constructProgrammeTitles(
          response.data.data
        );
        setAllPrograms(constructedProgrammeTitles);
        setPrograms(constructedProgrammeTitles);

        setAvailablePaginationParameters({
          totalCount: response.data.totalData,
          pageSize: response.data.size,
          currentPage: response.data.page,
          totalPages: response.data.totalPages,
        });
        setIsLoadingPrograms(false);
      });
    }
  };

  const getPrioritisedTitlesList = () => {
    setIsLoadingPrioritise(true);
    PrioritisationService.getPriorityList(prioritisation)
      .then((response) => {
        const { titles } = response.data;

        setPrioritisedPaginationParameters({
          totalCount: titles.length,
          pageSize: prioritisedParameters.size,
          currentPage: prioritisedParameters.page,
          totalPages: Math.ceil(titles.length / prioritisedParameters.size),
        });

        let sortedPrioritisedTitles = JSON.parse(JSON.stringify(titles));
        sortedPrioritisedTitles = sortedPrioritisedTitles.sort((a, b) => {
          if (prioritisedSort === 1) {
            if (a.title.toLowerCase() > b.title.toLowerCase()) return 1;
            return -1;
          } else {
            if (a.title.toLowerCase() > b.title.toLowerCase()) return -1;
            return 1;
          }
        });
        setAllPrioritisePrograms(sortedPrioritisedTitles);
        setSegments([]);

        setViewProgram();
        setTableMinimize(false);
        const prioritizePrograms = sortedPrioritisedTitles.slice(
          0,
          prioritisedParameters.size
        );

        setPrioritisedPrograms(prioritizePrograms);
        getPrograms(filterModel, searchKey, false, titles, true);
      })
      .finally(() => setIsLoadingPrioritise(false));
  };

  const handleAddProgram = () => {
    if (
      selectedProgramID === undefined ||
      selectedProgramID === null ||
      selectedProgramID.length === 0
    ) {
      danger('No selected Title');
    } else {
      let prioritised = allPrioritisePrograms;

      const programsAdded = addedPrograms;
      let programsRemoved = removedPrograms;
      selectedProgramID.map((value) => {
        const selectedProgram = allprograms.filter(
          (program) => program.id === value
        );
        if (selectedProgram[0] !== undefined) {
          // eslint-disable-next-line
          prioritised[prioritised.length] = selectedProgram[0];
        }

        if (programsRemoved.includes(value)) {
          const removeIndex = programsRemoved.indexOf(value);
          if (programsRemoved.length === 1) {
            programsRemoved = [];
          } else {
            programsRemoved = programsRemoved.splice(removeIndex - 1, 1);
          }
        } else {
          programsAdded[programsAdded.length] = value;
        }
        return true;
      });

      if (prioritisedSort === 1) {
        prioritised = prioritised.sort((a, b) => {
          if (a.title.toLowerCase() > b.title.toLowerCase()) return 1;
          return -1;
        });
      } else {
        prioritised = prioritised.sort((a, b) => {
          if (a.title.toLowerCase() > b.title.toLowerCase()) return -1;
          return 1;
        });
      }
      setAllPrioritisePrograms(prioritised);

      setSelectedProgramID([]);
      setHasSelectedPrograms(false);
      getPrioritisedPrograms(prioritised);
      getPrograms(filterModel, searchKey, true, prioritised);
      setIsDirty(programsAdded.length > 0 || programsRemoved.length > 0);
      setAddedPrograms(programsAdded);
      setRemovedPrograms(programsRemoved);
      setHasTitles(true);
    }
  };

  const handleSelectProgram = (programID) => {
    setSelectedProgramID(programID);
    setHasSelectedPrograms(programID.length > 0);
  };

  const handleSelectPriorityProgram = (programID) => {
    setSelectedPriorityProgramID(programID);
    setHasSelectedPrioritise(programID.length > 0);
  };

  const handleRemoveProgram = () => {
    if (
      selectedPriorityProgramID === undefined ||
      selectedPriorityProgramID === null ||
      selectedPriorityProgramID.length === 0
    ) {
      danger('No selected Title');
    } else {
      let prioritiseList = allPrioritisePrograms;

      let programsAdded = addedPrograms;
      const programsRemoved = removedPrograms;
      selectedPriorityProgramID.map((value) => {
        prioritiseList = prioritiseList.filter(
          (program) => program.id !== value
        );

        if (programsAdded.includes(value)) {
          const removeIndex = programsAdded.indexOf(value);
          if (programsAdded.length === 1) {
            programsAdded = [];
          } else {
            programsAdded = programsAdded.splice(removeIndex - 1, 1);
          }
        } else {
          programsRemoved[programsRemoved.length] = value;
        }
        return true;
      });

      setAllPrioritisePrograms(prioritiseList);
      setSelectedPriorityProgramID([]);
      setHasSelectedPrioritise(false);
      getPrioritisedPrograms(prioritiseList);

      setPrioritisedPaginationParameters({
        totalCount: prioritiseList.length,
        pageSize: prioritisedParameters.size,
        currentPage: prioritisedParameters.page,
        totalPages: Math.ceil(
          prioritiseList.length / prioritisedParameters.size
        ),
      });
      getPrograms(filterModel, searchKey, true, prioritiseList);

      setIsDirty(programsAdded.length > 0 || programsRemoved.length > 0);
      setAddedPrograms(programsAdded);
      setRemovedPrograms(programsRemoved);
      setHasTitles(true);
    }
  };

  const handleRemoveAll = () => {
    const emptyArray = [];
    setIsDirty(true);
    getPrioritisedPrograms(emptyArray);
    setAllPrioritisePrograms(emptyArray);
    getPrograms(filterModel, searchKey, true, emptyArray);
  };

  const getChannels = () => {
    PrioritisationService.getChannels().then((response) => {
      const channels = response.data;
      const channelList = [];
      channels.map((value) => {
        channelList.push({ id: value.channelId, name: value.channelName });
        return true;
      });
      setChannelOptions(channelList);
    });
  };

  const paginateAvalailablePrograms = (pageNumber, size) => {
    setHasSelectedPrograms(false);
    setSelectedProgramID([]);
    setSelectedPriorityProgramID([]);
    setHasSelectedPrioritise(false);
    setIsLoadingPrograms(true);
    setAvailableParameters({
      page: pageNumber,
      size,
    });
    setIsLoadingPrograms(false);
  };

  const paginatePrioritisedPrograms = (pageNumber, size) => {
    setHasSelectedPrograms(false);
    setSelectedProgramID([]);
    setSelectedPriorityProgramID([]);
    setHasSelectedPrioritise(false);
    setIsLoadingPrioritise(true);
    let toSkip = pageNumber === 1 ? 0 : (pageNumber - 1) * size;
    const programList = allPrioritisePrograms;
    let page = pageNumber;
    while (toSkip >= programList.length) {
      page -= 1;
      toSkip = page === 1 ? 0 : (page - 1) * size;
    }

    const priotizePrograms = programList.slice(toSkip, toSkip + size);
    setPrioritisedPrograms(priotizePrograms);
    setPrioritisedParameters({ page, size });

    setPrioritisedPaginationParameters({
      totalCount: programList.length,
      pageSize: size,
      currentPage: page,
      totalPages: Math.ceil(programList.length / size),
    });
    setIsLoadingPrioritise(false);
  };

  const handleSavePriotisation = () => {
    if (isTitleGroupDirty) {
      setSaveTitleGroupDirty(true);
      cleanDirtyForm();
      confirmNavigation();
    } else {
      let valid = true;
      const titleIDs = [];
      setIsSaving(true);
      if (!isDirty) {
        danger('Select Prioritised Title');
        valid = false;
      } else {
        allPrioritisePrograms.map((value) => {
          titleIDs.push(value.id);
          return true;
        });
      }
      let error = '';
      if (prioritisation.channelId == null) {
        error = 'Channel';

        valid = false;
      }
      if (prioritisation.month == null) {
        error = error === '' ? 'Month' : `${error}, Month`;
        valid = false;
      }
      if (prioritisation.year == null) {
        error = error === '' ? 'Year' : `${error}, Year`;
        valid = false;
      }

      if (error !== '') {
        danger(`${error} is required`);
      }

      if (valid) {
        const prioritisationParameters = {
          titleIds: titleIDs,
          channelId: prioritisation.channelId,
          month: prioritisation.month,
          year: prioritisation.year,
        };
        PrioritisationService.savePriotisation(prioritisationParameters)
          .then(() => {
            cleanDirtyForm();
            setHasTitles(false);
            setIsSaving(false);
            success('Prioritisation Saved');
            confirmNavigation();
          })
          .finally(() => {
            cancelNavigation();
          });
      } else {
        setIsSaving(false);
      }
    }
    handleChangeTab();
  };

  const handleSort = (id) => {
    const newArr = sortBy;

    if (newArr[id].state === true) {
      newArr[id].state = false;
    } else {
      newArr[id].state = true;
    }

    setSortBy(newArr);
  };

  const searchRef = useRef(null);

  useEffect(() => {
    setViewProgram();
    setViewedProgram(null);
    setTableMinimize(false);
  }, [activeTab]);

  useEffect(() => {
    if (
      prioritisedPaginationParameters.currentPage >
      prioritisedPaginationParameters.totalPages
    ) {
      setPrioritisedPaginationParameters((prevState) => ({
        ...prevState,
        currentPage: prioritisedPaginationParameters.totalPages,
      }));
      setPrioritisedParameters((prevState) => ({
        ...prevState,
        page: prioritisedPaginationParameters.totalPages,
      }));

      const { size } = prioritisedParameters;
      const toSkip =
        prioritisedPaginationParameters.totalPages === 1
          ? 0
          : (prioritisedPaginationParameters.totalPages - 1) * size;
      const selectedItems = prioritisedPrograms.slice(toSkip, toSkip + size);
      setPrioritisedPrograms(selectedItems);
    }

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [prioritisedPaginationParameters]);

  useEffect(() => {
    const backdropClicked = (e) => {
      if (
        saveSearchKey &&
        searchRef.current &&
        !searchRef.current.contains(e.target)
      ) {
        setSearchKey(saveSearchKey);
        setClearSearchIsActive(true);
      }
    };

    document.addEventListener('mousedown', backdropClicked);
    return () => document.removeEventListener('mousedown', backdropClicked);
  }, [searchRef, saveSearchKey]);

  const onChangeSearchKey = (e) => {
    setSearchKey(e.target.value);
    setClearSearchIsActive(false);
  };

  const searchList = () => {
    getPrograms(filterModel, searchKey, true, allPrioritisePrograms);
    setSaveSearchKey(searchKey);
    setClearSearchIsActive(true);
  };

  const clearSearchList = () => {
    setSearchKey('');
    setSaveSearchKey('');
    getPrograms(filterModel, '', true, allPrioritisePrograms);
    setClearSearchIsActive(false);
  };

  const triggerSearchList = (e) => {
    if (e.key === 'Enter') searchList();
  };

  const handleSortPrioritised = (model) => {
    setIsLoadingPrioritise(true);
    setPrioritisedSort(model.sortBy);
    let programList = allPrioritisePrograms;
    if (model.sortBy === 1) {
      programList = programList.sort((a, b) => {
        if (a.title.toLowerCase() > b.title.toLowerCase()) return 1;
        return -1;
      });
    } else {
      programList = programList.sort((a, b) => {
        if (a.title.toLowerCase() > b.title.toLowerCase()) return -1;
        return 1;
      });
    }
    setAllPrioritisePrograms(programList);

    let pageNumber =
      programList.length > 0 && prioritisedParameters.page === 0
        ? 1
        : prioritisedParameters.page;
    const { size } = prioritisedParameters;
    let toSkip = pageNumber === 1 ? 0 : (pageNumber - 1) * size;

    while (toSkip >= programList.length) {
      pageNumber -= 1;
      toSkip = pageNumber === 1 ? 0 : (pageNumber - 1) * size;
    }
    const priotizePrograms = programList.slice(toSkip, toSkip + size);
    setPrioritisedPrograms(priotizePrograms);

    setPrioritisedParameters({ page: pageNumber, size });

    setPrioritisedPaginationParameters({
      totalCount: programList.length,
      pageSize: size,
      currentPage: pageNumber,
      totalPages: Math.ceil(programList.length / size),
    });

    setIsLoadingPrioritise(false);
  };
  const handleChangeFileInput = () => {
    const { files } = fileUploadRef.current;
    setFileNameUploaded(files[0].name);
  };
  const handleImportFile = () => {
    setLoadingUpload(true);
    const { files } = fileUploadRef.current;
    const file = files[0];
    setUploadError('');
    if (file === undefined) {
      setUploadError('No file selected');
      setLoadingUpload(false);
    } else {
      const fileExtension = file.name.split('.').pop();
      const formData = new FormData();
      formData.append('file', file);
      if (fileExtension === 'xlsx' || fileExtension === 'csv') {
        PrioritisationService.uploadFile(
          formData,
          prioritisation.channelId,
          prioritisation.year,
          prioritisation.month,
          fileExtension
        )
          .then((response) => {
            setModalShow(false);
            setModalSuccessShow(true);
            let count = 0;
            response.data.items.map((value) => {
              if (value.successfullyImported) count += 1;
              return true;
            });
            getPrograms(filterModel, searchKey, false, allPrioritisePrograms);
            setSuccessCount(count);
            setAllUploadedTitles(response.data.items);
            setUploadedTitles(response.data.items);
            setUploadedTitleFilter('all');
          })
          .catch((error) => {
            setUploadError(error.response.data.message);
          })
          .finally(() => {
            setLoadingUpload(false);
          });
      } else {
        setUploadError(
          'Uploaded file type is not supported. Please use files in the following format (xlsx)'
        );
      }
      setLoadingUpload(false);
    }
  };

  const handleChangeRadio = (name) => {
    setUploadedTitleFilter(name);
    if (name === 'imported') {
      setUploadedTitles(
        allUploadedTitles.filter((o) => o.successfullyImported)
      );
    } else if (name === 'notimported') {
      setUploadedTitles(
        allUploadedTitles.filter((o) => !o.successfullyImported)
      );
    } else {
      setUploadedTitles(allUploadedTitles);
    }
  };

  const exportExcelFile = () => {
    PrioritisationService.exportUploadSample().then((response) => {
      const blob = new Blob([response.data], {
        type:
          'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet',
      });
      saveAs(blob, 'sample_titles_export.xlsx');
    });
  };

  const handleProceedImport = () => {
    setModalVerifyShow(false);
    setModalShow(true);
  };

  const handleCloseSegments = () => {
    setSegments([]);
    setTableMinimize(false);
  };

  const getSegments = (programID) => {
    PrioritisationService.getSegments(programID).then((response) => {
      setTableMinimize(true);
      if (segments.length === 0) {
        setSegments(response.data.segments);
      } else if (response.data.segments[0].id !== segments[0].id) {
        setSegments(response.data.segments);
      } else handleCloseSegments();
    });
  };

  const generateSegments = () => {
    setModalVerifySegmentsShow(false);
    setModalLoadingShow(true);

    PrioritisationService.generateSegments(prioritisation).then((response) => {
      let count = 0;
      response.data.items.map((value) => {
        if (value.segmentsUpdated) count += 1;
        return true;
      });
      getPrograms(filterModel, searchKey, false, allPrioritisePrograms);
      setSuccessCount(count);
      setAllUploadedTitles(response.data.items);
      setUploadedTitles(response.data.items);
      setUploadedTitleFilter('all');
      setModalLoadingShow(false);
      setModalSegmentsShow(true);
    });
  };

  const handleChangeSegmentsRadio = (name) => {
    setUploadedTitleFilter(name);
    if (name === 'updated') {
      setUploadedTitles(allUploadedTitles.filter((o) => o.segmentsUpdated));
    } else if (name === 'notfound') {
      setUploadedTitles(allUploadedTitles.filter((o) => !o.segmentsUpdated));
    } else {
      setUploadedTitles(allUploadedTitles);
    }
  };

  const handleGetSegments = () => {
    if (hasSegments) setModalVerifySegmentsShow(true);
    else generateSegments();
  };

  const constructPrioritisationSessionParameters = (parameters) => {
    const channelIdParam =
      parameters.channelId || parameters.channelID
        ? `?channelId=${
            parameters.channelId ? parameters.channelId : parameters.channelID
          }`
        : '';
    const monthParam = parameters.monthID
      ? `&monthID=${parameters.monthID}`
      : '';
    const yearParam = parameters.year
      ? `&year=${parameters.year}`
      : `&year=${yearOptions[0].id}`;

    return `${channelIdParam}${monthParam}${yearParam}`;
  };

  const handleGetPrioritisationSession = () => {
    PrioritisationService.getPrioritisationSession().then((response) => {
      const channel = channelOptions.find(
        (o) => o.id === response.data.channelID
      );
      const month =
        response.data.monthID > 0
          ? monthOptions.find((o) => o.id === response.data.monthID)
          : monthOptions[0];
      const year = yearOptions.find(
        (o) => o.name.toString() === response.data.year
      );
      setPrioritisationSessionParameters(response.data);
      channel && setSelectedChannel(channel.name);
      if(year){
        setSelectedYear(year.id)
      };
      setSelectedMonth(month?.value);
      setPrioritisation({
        ...prioritisation,
        channelId: channel ? channel.id : null,
        month: month.id,
        year: year?.id,
      });
    });
  };

  const handleSavePrioritisationSession = () => {
    PrioritisationService.savePrioritisationSession(
      constructPrioritisationSessionParameters(prioritisationSessionParameters)
    );
  };

  const handleSelectChannel = (selected) => {
    if (isDirty || isTitleGroupDirty) {
      setNextFilter({ input: 'channel', selected });
      setShowConfirmLeaveModal(true);
    } else {
      setSearchKey('');
      setAllPrioritisePrograms([]);
      setClearSearchIsActive(false);
      setSelectedChannel(selected.name);
      setPrioritisation((prevState) => ({
        ...prevState,
        channelId: selected.id,
      }));
      setFilterModel({ ...filterModel, channelId: selected.id });
      onChangeFormSetPrioritisationSessionParameters({
        channelID: selected.id,
      });
    }
  };

  const handleSelectMonth = (selected) => {
    if (isDirty || isTitleGroupDirty) {
      setNextFilter({ input: 'month', selected });
      setShowConfirmLeaveModal(true);
    } else {
      setSearchKey('');
      setClearSearchIsActive(false);
      setSelectedMonth(selected.value);
      setPrioritisation((prevState) => ({
        ...prevState,
        month: selected.id,
      }));
      onChangeFormSetPrioritisationSessionParameters({ monthID: selected.id });
    }
  };

  const handleSelectYear = (selected) => {
    if (isDirty || isTitleGroupDirty) {
      setNextFilter({ input: 'year', selected });
      setShowConfirmLeaveModal(true);
    } else {
      setSearchKey('');
      setClearSearchIsActive(false);
      setSelectedYear(selected.id);
      setPrioritisation((prevState) => ({
        ...prevState,
        year: selected.id,
      }));
      onChangeFormSetPrioritisationSessionParameters({ year: selected.id });
    }
  };

  const cancelDirtyForm = () => {
    setNextTab();
    setNextFilter({});
  };

  const isPrioritisationNotNull = () => {
    return (
      prioritisation.channelId &&
      prioritisation.month &&
      prioritisation.year &&
      prioritisation.channelId !== null &&
      prioritisation.month !== null &&
      prioritisation.year !== null
    );
  };

  useEffect(() => {
    getChannels();
    ProfileService.getUser(AuthService.getUserIdentity().userId).then(
      (response) => {
        setTitleSource(response.data.organisationTitleSource);
      }
    );
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  useEffect(() => {
    if (isPrioritisationNotNull() && activeTab === 'Titles') {
      getPrograms(filterModel, searchKey, false, allPrioritisePrograms, true);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [filterModel, activeTab]);

  useEffect(() => {
    if (isPrioritisationNotNull() && activeTab === 'Titles') {
      getPrioritisedTitlesList();
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [prioritisation, activeTab]);

  useEffect(() => {
    if (isPrioritisationNotNull()) {
      getPrograms(filterModel, searchKey, true, allPrioritisePrograms);
    }
    /* eslint-disable react-hooks/exhaustive-deps */
  }, [availableParameters]);

  useEffect(() => {
    if (channelOptions.length >= 1) {
      handleGetPrioritisationSession();
    }
    /* eslint-disable react-hooks/exhaustive-deps */
  }, [channelOptions]);

  useEffect(() => {
    if (prioritisationSessionParameters) {
      if (renderTimes > 0) {
        handleSavePrioritisationSession();
      }
    }
    setRenderTimes(renderTimes + 1);
    /* eslint-disable react-hooks/exhaustive-deps */
  }, [prioritisationSessionParameters]);

  useEffect(() => {
    if (!isTitleGroupDirty && Object.keys(nextFilter).length > 0) {
      handleDiscardChanges();
    }
    /* eslint-disable react-hooks/exhaustive-deps */
  }, [isTitleGroupDirty]);

  useEffect(() => {
    if (nextTab) {
      if (isDirty || isTitleGroupDirty) setShowConfirmLeaveModal(true);
    }
    /* eslint-disable react-hooks/exhaustive-deps */
  }, [nextTab]);

  const segmentButtonColor = () => {
    if (!hasTitles) return '#b1b1b1';
    if (hasSegments) return '#008662';
    return '#FF7878';
  };

  const tabs = [
    {
      id: 1,
      label: 'Titles',
      component: (
        <>
          <div className="d-flex">
            <div style={{ width: '60%' }}>
              {selectedChannel === 'Please select a channel' ? (
                <div
                  className={
                    tableMinimize
                      ? Styles.divNoAvailableChannelSelectedSmall
                      : Styles.divNoAvailableChannelSelected
                  }
                >
                  <div
                    style={{ display: 'table-cell', verticalAlign: 'middle' }}
                  >
                    <div>
                      Please select a channel to list its available programme
                      titles.
                    </div>
                  </div>
                </div>
              ) : (
                <div>
                  {!prioritisation.channelId ? (
                    <div className={`${Styles.divNoAvailableChannelSelected}`}>
                      <div
                        style={{
                          display: 'table-cell',
                          verticalAlign: 'middle',
                        }}
                      >
                        {titleSource === 2 ? (
                          <>
                            <div>
                              No available programme titles in this channel
                              covering the selected period.
                            </div>
                            <div style={{ marginTop: '40px' }}>
                              You can add program titles by clicking import.
                            </div>
                          </>
                        ) : (
                          <div>
                            Please select a channel to list its available
                            programme titles.
                          </div>
                        )}
                      </div>
                    </div>
                  ) : (
                    <>
                      <div className={Styles.tableLabel}>
                        Available programme titles:
                      </div>

                      <AvailableProgramsComponent
                        ref={availableProgramsRef}
                        programs={programs}
                        callbackViewProgram={handleViewProgram}
                        handleSelectProgram={handleSelectProgram}
                        isLoading={isLoadingPrograms || isLoadingPrioritise}
                        availablePaginationParameters={
                          availablePaginationParameters
                        }
                        paginate={paginateAvalailablePrograms}
                        selectedPrograms={selectedProgramID}
                        handleSetSortBy={handleSort}
                        setFilterModel={setFilterModel}
                        filterModel={filterModel}
                        selectedChannel={selectedChannel}
                        callbackViewSegment={getSegments}
                        viewedProgram={viewedProgram}
                        tableMinimize={tableMinimize}
                        titleSource={titleSource}
                      />
                    </>
                  )}
                </div>
              )}
            </div>

            <div className={Styles.divButtons}>
              <button
                onClick={handleAddProgram}
                disabled={!hasSelectedPrograms}
                type="button"
              >
                Add Selected
                <img style={{ marginTop: '11px' }} src={arrowLeftIcon} alt="" />
              </button>
              <button
                onClick={handleRemoveAll}
                style={{ paddingTop: '9px' }}
                disabled={allPrioritisePrograms.length === 0}
                type="button"
              >
                <img style={{ marginTop: '2px' }} src={arrowRightIcon} alt="" />
                <span style={{ marginLeft: '5px' }}>Remove All</span>
              </button>
              <button
                onClick={handleRemoveProgram}
                disabled={
                  !hasSelectedPrioritise || allPrioritisePrograms.length === 0
                }
                type="button"
              >
                <img
                  style={{ marginTop: '11px' }}
                  src={arrowRightIcon}
                  alt=""
                />
                Remove Selected
              </button>
            </div>

            <div style={{ width: '33%' }}>
              {selectedChannel === 'Please select a channel' ||
              (allPrioritisePrograms.length === 0 && !isLoadingPrioritise) ? (
                <div
                  className={`${
                    tableMinimize
                      ? Styles.divNoAvailableChannelSelectedSmall
                      : Styles.divNoAvailableChannelSelected
                  }`}
                  style={{ fontSize: '28px' }}
                >
                  <div
                    style={{ display: 'table-cell', verticalAlign: 'middle' }}
                  >
                    <div>
                      Please select and add programme titles to prioritise
                    </div>
                  </div>
                </div>
              ) : (
                <>
                  <div className={Styles.tableLabel}>
                    Prioritised programme titles:
                  </div>
                  <PrioritisedProgramsComponent
                    ref={prioritisedProgramRef}
                    programs={prioritisedPrograms}
                    callbackViewProgram={handleViewPriorityProgram}
                    handleSelectProgram={handleSelectPriorityProgram}
                    paginationParameters={prioritisedPaginationParameters}
                    paginate={paginatePrioritisedPrograms}
                    selectedPrograms={selectedPriorityProgramID}
                    viewProgram={viewProgram}
                    handleSortPrioritised={handleSortPrioritised}
                    callbackViewSegment={getSegments}
                    tableMinimize={tableMinimize}
                    titleSource={titleSource}
                    viewedProgram={viewedProgram}
                    isLoading={isLoadingPrioritise}
                  />
                </>
              )}
            </div>
          </div>

          {viewedProgram && (
            <div style={{ width: '100%', marginTop: '8px' }}>
              {titleSource === 1 ? (
                <ProgramDetailsComponent
                  component="Prioritisation"
                  program={viewProgram}
                  callbackCloseDetails={handleCloseDetails}
                />
              ) : (
                <ImportProgramDetailsComponent
                  component="Prioritisation"
                  program={viewProgram}
                  callbackCloseDetails={handleCloseDetails}
                />
              )}
            </div>
          )}

          <div style={{ float: 'right', marginTop: '8px' }}>
            <Button
              text="Save"
              isLoading={isSaving}
              onClick={handleSavePriotisation}
              disabled={!isDirty}
            />
          </div>
        </>
      ),
    },
    {
      id: 2,
      label: 'Title Groups',
      component: (
        <TitleGroupComponent
          prioritisation={prioritisation}
          setChanged={(e) => setIsTitleGroupDirty(e)}
          changed={isTitleGroupDirty}
          discardTitleGroupDirty={discardTitleGroupDirty}
          setDiscardTitleGroupDirty={(e) => setDiscardTitleGroupDirty(e)}
          saveTitleGroupDirty={saveTitleGroupDirty}
          setSaveTitleGroupDirty={(e) => setSaveTitleGroupDirty(e)}
        />
      ),
    },
  ];

  return (
    <div>
      <BannerComponent
        componentName="Prioritisation"
        isAddButtonHidden
        isSearchHidden
      />

      <hr style={{ background: 'rgb(200, 200, 200)' }} />
      <div className={`row ${Styles.marginBottom}`}>
        <div style={{ width: '80px' }}>
          <div className={Styles.channelLabel}>Channel:</div>
        </div>
        <div style={{ width: '300px' }}>
          <SelectComponent
            text={selectedChannel}
            list={channelOptions}
            onSelect={handleSelectChannel}
            width="300px"
            withSearch
          />
        </div>
        <div style={{ width: 'auto', marginLeft: '10px' }}>
          <div className={Styles.channelLabel}>Month:</div>
        </div>
        <div style={{ width: '180px', paddingLeft: '0' }}>
          <SelectComponentCustom
            text={selectedMonth}
            list={monthOptions}
            onSelect={handleSelectMonth}
            width="180px"
          />
        </div>
        <div style={{ width: 'auto', marginLeft: '10px' }}>
          <div className={Styles.channelLabel}>Year:</div>
        </div>
        <div style={{ width: '150px', paddingLeft: '0' }}>
          <SelectComponent
            text={selectedYear}
            list={yearOptions}
            onSelect={handleSelectYear}
            width="150px"
          />
        </div>

        <button
          className={`${Styles.importButton}`}
          style={{
            display: titleSource === 2 ? null : 'none',
            cursor: 'pointer',
            backgroundColor:
              selectedChannel === 'Please select a channel'
                ? '#b1b1b1'
                : '#008662',
          }}
          onClick={() => {
            setFileNameUploaded('');
            setUploadError('');
            if (allprograms.length === 0) setModalShow(true);
            else setModalVerifyShow(true);
          }}
          disabled={selectedChannel === 'Please select a channel'}
          type="button"
        >
          Import
        </button>

        <button
          className={`${Styles.importButton}`}
          style={{
            display: titleSource === 2 ? null : 'none',
            cursor: hasTitles ? 'pointer' : 'not-allowed',
            backgroundColor: segmentButtonColor(),
            width: 'auto',
          }}
          disabled={!hasTitles}
          onClick={handleGetSegments}
          type="button"
        >
          {hasSegments ? 'Update Segments' : 'Get Segments'}
        </button>
      </div>

      <TabsComponent
        activeTab={activeTab}
        labelList={tabs}
        dirty={isDirty || isTitleGroupDirty}
        tabChange={(e) => setNextTab(e)}
        setActive={(e) => setActiveTab(e)}
      />

      <Modal
        size="lg"
        show={modalShow}
        onHide={() => setModalShow(false)}
        aria-labelledby="example-modal-sizes-title-lg"
      >
        <Modal.Body style={{ padding: '35px' }}>
          {loadingUpload ? (
            <div
              style={{
                fontSize: '50px',
                color: '#13ad83',
                backgroundColor: 'transparent',
                position: 'relative',
                height: '145px',
              }}
            >
              <div
                style={{
                  fontSize: '16px',
                  color: '#000',
                  textAlign: 'center',
                  width: '715px',
                }}
              >
                Importing and validation programme titles from sample.csv...
              </div>

              <i
                className="fas fa-circle-notch fa-spin mr-1"
                style={{
                  top: 0,
                  bottom: 0,
                  left: 0,
                  right: 0,
                  margin: 'auto',
                  position: 'absolute',
                  height: '50px',
                  width: '50px',
                }}
              />
            </div>
          ) : (
            <>
              <div style={{ fontWeight: 'bolder' }}>Import program titles</div>
              <div>
                <small>
                  Download a{' '}
                  <small
                    style={{
                      color: '#008662',
                      textDecoration: 'none',
                      cursor: 'pointer',
                    }}
                    onClick={exportExcelFile}
                    onKeyDown={exportExcelFile}
                    role="presentation"
                  >
                    sample template
                  </small>{' '}
                  to see an example of the required format.
                </small>
              </div>
              <div>
                <small style={{ color: 'red' }}>{uploadError}</small>
              </div>
              <button
                style={{
                  marginTop: '30px',
                  marginLeft: '0',
                }}
                className={Styles.importButton}
                onClick={() => {
                  fileUploadRef.current.click();
                }}
                type="button"
              >
                Choose file
              </button>
              <small style={{ marginLeft: '15px' }}>{fileNameUploaded}</small>
              <input
                ref={fileUploadRef}
                type="file"
                accept="application/vnd.openxmlformats-officedocument.spreadsheetml.sheet, application/vnd.ms-excel, .csv"
                style={{ display: 'none' }}
                onChange={handleChangeFileInput}
              />
              <div
                style={{
                  paddingTop: '10px',
                }}
              >
                <small>Acceptable formats: Excel, CSV</small>
              </div>

              <div
                style={{
                  textAlign: 'right',
                  paddingTop: '30px',
                }}
              >
                <button
                  className={Styles.cancelButton}
                  onClick={() => setModalShow(false)}
                  type="button"
                >
                  Cancel
                </button>
                <button
                  className={Styles.importButton}
                  onClick={handleImportFile}
                  type="button"
                >
                  Import
                </button>
              </div>
            </>
          )}
        </Modal.Body>
      </Modal>

      <Modal
        size="lg"
        show={modalSuccessShow}
        onHide={() => setModalSuccessShow(false)}
        aria-labelledby="example-modal-sizes-title-lg"
      >
        <Modal.Body style={{ padding: '35px' }}>
          <div style={{ textAlign: 'center' }}>
            <b>{` ${successCount} `}</b>
            programs out of
            <b>{` ${allUploadedTitles.length} `}</b>
            were successfully imported and validated!
          </div>

          <div className={`${Styles.successDiv}`}>
            <table>
              <tbody className={`${Styles.successBody}`}>
                {uploadedTitles.map((value) => (
                  <tr
                    key={value.id}
                    className={`${Styles.trBorder}`}
                    style={{ width: '100%' }}
                  >
                    <td className="table-body-column" style={{ width: '90%' }}>
                      {value.title}
                    </td>
                    <td className="table-body-column">
                      <i>
                        <img
                          src={value.successfullyImported ? checkIcon : xIcon}
                          alt=""
                        />
                      </i>
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
          <div style={{ marginTop: '10px' }}>
            <div
              className="form-check"
              style={{ marginRight: '20px', display: 'inline-block' }}
              onClick={() => handleChangeRadio('all')}
              onKeyDown={() => handleChangeRadio('all')}
              role="presentation"
            >
              <input
                className={`${Styles.formCheck} form-check-input`}
                type="radio"
                name="successFilter"
                id="flexRadioDefault1"
                checked={uploadedTitleFilter === 'all'}
              />
              <div className="form-check-label" htmlFor="flexRadioDefault1">
                Show all
              </div>
            </div>

            <div
              className="form-check"
              style={{ marginRight: '20px', display: 'inline-block' }}
              onClick={() => handleChangeRadio('imported')}
              onKeyDown={() => handleChangeRadio('imported')}
              role="presentation"
            >
              <input
                className={`${Styles.formCheck} form-check-input`}
                type="radio"
                name="successFilter"
                id="flexRadioDefault2"
                checked={uploadedTitleFilter === 'imported'}
              />
              <div className="form-check-label" htmlFor="flexRadioDefault2">
                Imported
              </div>
            </div>

            <div
              className="form-check"
              style={{ marginRight: '20px', display: 'inline-block' }}
              onClick={() => handleChangeRadio('notimported')}
              onKeyDown={() => handleChangeRadio('notimported')}
              role="presentation"
            >
              <input
                className={`${Styles.formCheck} form-check-input`}
                type="radio"
                name="successFilter"
                id="flexRadioDefault3"
                checked={uploadedTitleFilter === 'notimported'}
              />
              <div className="form-check-label" htmlFor="flexRadioDefault3">
                Not imported
              </div>
            </div>
          </div>
          <div
            style={{
              textAlign: 'right',
              paddingTop: '30px',
            }}
          >
            <button
              className={Styles.saveButton}
              onClick={() => setModalSuccessShow(false)}
              style={{ marginRight: '0' }}
              type="button"
            >
              Close
            </button>
          </div>
        </Modal.Body>
      </Modal>

      <Modal
        size="md"
        show={modalVerifyShow}
        onHide={() => setModalVerifyShow(false)}
        aria-labelledby="example-modal-sizes-title-lg"
      >
        <Modal.Body style={{ padding: '35px' }}>
          <div style={{ textAlign: 'center' }}>
            This channel already have existing programme titles.{' '}
          </div>
          <div style={{ textAlign: 'center', marginTop: '20px' }}>
            <b> Are you sure you want to proceed?</b>
          </div>
          <div
            style={{
              textAlign: 'center',
              paddingTop: '30px',
            }}
          >
            <button
              className={Styles.cancelButton}
              onClick={() => setModalVerifyShow(false)}
              style={{ marginLeft: '0' }}
              type="button"
            >
              Cancel
            </button>
            <button
              className={Styles.importButton}
              onClick={handleProceedImport}
              type="button"
            >
              Proceed
            </button>
          </div>
        </Modal.Body>
      </Modal>

      <Modal
        size="lg"
        show={modalLoadingShow}
        onHide={() => setModalLoadingShow(false)}
        aria-labelledby="example-modal-sizes-title-lg"
      >
        <Modal.Body style={{ padding: '35px' }}>
          <div>
            <div
              style={{
                fontSize: '50px',
                color: '#13ad83',
                backgroundColor: 'transparent',
                position: 'relative',
                height: '145px',
              }}
            >
              <div
                style={{
                  fontSize: '16px',
                  color: '#000',
                  textAlign: 'center',
                  width: '715px',
                }}
              >
                Updating Segment details...
              </div>

              <i
                className="fas fa-circle-notch fa-spin mr-1"
                style={{
                  top: 0,
                  bottom: 0,
                  left: 0,
                  right: 0,
                  margin: 'auto',
                  position: 'absolute',
                  height: '50px',
                  width: '50px',
                }}
              />
            </div>
          </div>
        </Modal.Body>
      </Modal>

      <Modal
        size="lg"
        show={modalSegmentsShow}
        onHide={() => setModalSegmentsShow(false)}
        aria-labelledby="example-modal-sizes-title-lg"
      >
        <Modal.Body style={{ padding: '35px' }}>
          <div style={{ textAlign: 'center' }}>
            Segments updated for <b>{` ${successCount} `}</b>
            programme titles out of <b>{allUploadedTitles.length}</b>!
          </div>

          <div className={`${Styles.successDiv}`}>
            <table>
              <tbody className={`${Styles.successBody}`}>
                {uploadedTitles.map((value) => (
                  <tr
                    key={value.id}
                    className={`${Styles.trBorder}`}
                    style={{ width: '100%' }}
                  >
                    <td className="table-body-column" style={{ width: '90%' }}>
                      {value.title}
                    </td>
                    <td className="table-body-column">
                      <i>
                        <img
                          src={value.segmentsUpdated ? checkIcon : xIcon}
                          alt=""
                        />
                      </i>
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
          <div style={{ marginTop: '10px' }}>
            <div
              className="form-check"
              style={{ marginRight: '20px', display: 'inline-block' }}
              onClick={() => handleChangeSegmentsRadio('all')}
              onKeyDown={() => handleChangeSegmentsRadio('all')}
              role="presentation"
            >
              <input
                className={`${Styles.formCheck} form-check-input`}
                type="radio"
                name="segmentsuccessFilter"
                id="flexRadioDefault6"
                checked={uploadedTitleFilter === 'all'}
              />
              <div className="form-check-label" htmlFor="flexRadioDefault6">
                Show all
              </div>
            </div>

            <div
              className="form-check"
              style={{ marginRight: '20px', display: 'inline-block' }}
              onClick={() => handleChangeSegmentsRadio('updated')}
              onKeyDown={() => handleChangeSegmentsRadio('updated')}
              role="presentation"
            >
              <input
                className={`${Styles.formCheck} form-check-input`}
                type="radio"
                name="segmentsuccessFilter"
                id="flexRadioDefault4"
                checked={uploadedTitleFilter === 'updated'}
              />
              <div className="form-check-label" htmlFor="flexRadioDefault4">
                Segments Updated
              </div>
            </div>

            <div
              className="form-check"
              style={{ marginRight: '20px', display: 'inline-block' }}
              onClick={() => handleChangeSegmentsRadio('notfound')}
              onKeyDown={() => handleChangeSegmentsRadio('notfound')}
              role="presentation"
            >
              <input
                className={`${Styles.formCheck} form-check-input`}
                type="radio"
                name="segmentsuccessFilter"
                id="flexRadioDefault5"
                checked={uploadedTitleFilter === 'notfound'}
              />
              <div className="form-check-label" htmlFor="flexRadioDefault5">
                No Segments Found
              </div>
            </div>
          </div>
          <div
            style={{
              textAlign: 'right',
              paddingTop: '30px',
            }}
          >
            <button
              className={Styles.saveButton}
              onClick={() => setModalSegmentsShow(false)}
              style={{ marginRight: '0' }}
              type="button"
            >
              Close
            </button>
          </div>
        </Modal.Body>
      </Modal>

      <Modal
        size="md"
        show={modalVerifySegmentsShow}
        onHide={() => setModalVerifySegmentsShow(false)}
        aria-labelledby="example-modal-sizes-title-lg"
      >
        <Modal.Body style={{ padding: '35px' }}>
          <div style={{ textAlign: 'center' }}>
            The programme titles already have existing segments.{' '}
          </div>
          <div style={{ textAlign: 'center' }}>
            Updating their segments will delete all existing segments of the
            programme titles.
          </div>
          <div style={{ textAlign: 'center', marginTop: '20px' }}>
            <b> Are you sure you want to proceed?</b>
          </div>
          <div
            style={{
              textAlign: 'center',
              paddingTop: '30px',
            }}
          >
            <button
              className={Styles.cancelButton}
              onClick={() => setModalVerifySegmentsShow(false)}
              style={{ marginLeft: '0' }}
              type="button"
            >
              Cancel
            </button>
            <button
              className={Styles.importButton}
              onClick={generateSegments}
              type="button"
            >
              Proceed
            </button>
          </div>
        </Modal.Body>
      </Modal>

      <ConfirmLeavePageModalComponent
        show={showPrompt}
        onHide={hidePrompt}
        onSave={handleSavePriotisation}
        onDiscard={confirmNavigation}
      />

      <ConfirmLeavePageModalComponent
        show={showConfirmLeaveModal}
        onHide={() => {
          setShowConfirmLeaveModal(false);
          cancelDirtyForm();
        }}
        onSave={() => {
          handleSavePriotisation();
          handleDiscardChanges();
        }}
        onDiscard={handleDiscardChanges}
      />
    </div>
  );
}

export default PrioritisationComponent;
