import React, { useEffect, useState } from 'react';
import closeIcon from '../../../assets/icons/Close.svg';
import styles from './TitleGroups.module.css';
import PrioritisationService from '../PrioritisationService';
import DateService from '../../../core/date.service';

export default function TitleGroupDetailsComponent({
  viewGroupDetails,
  callbackCloseDetails,
  prioritisation,
}) {
  const [titles, setTitles] = useState([]);
  const handleGetTitleGroupDetails = () => {
    var start = DateService.toIsoFormat(
      new Date(Date.UTC(prioritisation.year, prioritisation.month - 1, 1))
    );
    var end = DateService.toIsoFormat(
      new Date(Date.UTC(prioritisation.year, prioritisation.month, 0))
    );
    PrioritisationService.getTitleGroupPrograms(
      viewGroupDetails.id,
      prioritisation.channelId,
      start,
      end
    ).then((response) => {
      let responseData = JSON.parse(JSON.stringify(response.data.data));
      responseData = responseData.sort((a, b) => {
        if (a.titleName.toLowerCase() > b.titleName.toLowerCase()) return 1;
        return -1;
      });
      setTitles(responseData);
    });
  };

  useEffect(() => {
    viewGroupDetails && handleGetTitleGroupDetails();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [viewGroupDetails]);
  return (
    <>
      <div style={{ width: '100%', marginTop: '8px' }}>
        <div className="col-12">
          <div className={styles.panel}>
            <div className="position-relative">
              <i>
                <img
                  className={styles.closeIcon}
                  src={closeIcon}
                  alt=""
                  onClick={callbackCloseDetails}
                  onKeyDown={callbackCloseDetails}
                  role="presentation"
                />
              </i>
            </div>
            <div style={{ marginBottom: '10px' }}>
              <b
                style={{ fontSize: '20px', fontWeight: 600, fontSize: '16px' }}
              >
                {viewGroupDetails.name}
              </b>
            </div>
            <div className="col-12">
              <b className={styles.labelField}>Programme titles:</b>
            </div>{' '}
            <div className={styles.scroll}>
              <div className="col-12">
                {titles.map((value) => (
                  <b className={styles.fontBold}>{value.titleName}</b>
                ))}
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}
