import React, { useState, useEffect } from 'react';
import Table from '../../../../shared/components/table/Table';
import { TitleGroupStorage } from '../TitleGroupStorage';
import SearchAvailableTitleGroupComponent from './SearchAvailableTitleGroupComponent';
import Styles from '../../Prioritisation.module.css';

function AvailableTitleGroupComponent({
  selectTitleGroups,
  groups,
  paginationParameters,
  clearSelected,
  setClearSelected,
  selectDetail,
  tableHeight,
  handleGetTitles,
  isLoading,
  queryParameters,
  setQueryParameters,
  sortParameters,
  setSortParameters,
  prioritisation,
}) {
  const tableHeaders = TitleGroupStorage.availableTitleGroupHeaders;
  const [tableParameters, setTableParameters] = useState({});

  const handleOnAction = (actionName, selectedId) => {
    selectDetail(selectedId);
  };

  const handleOnFilter = (selectedData) => {
    setQueryParameters({
      ...queryParameters,
      ...selectedData,
      pageNumber: 1,
      pageSize: queryParameters.pageSize,
      searchKey: queryParameters.searchKey,
    });
    setTableParameters({
      ...queryParameters,
      ...selectedData,
      pageNumber: 1,
      pageSize: queryParameters.pageSize,
      searchKey: queryParameters.searchKey,
    });
  };

  const handleOnClearFilter = () => {
    setQueryParameters({
      pageNumber: 1,
      pageSize: queryParameters.pageSize,
      searchKey: queryParameters.searchKey,
    });
    setTableParameters({});
  };

  const handleOnSearch = (searchKey) => {
    setQueryParameters({
      ...queryParameters,
      pageNumber: 1,
      pageSize: queryParameters.pageSize,
      searchKey,
    });
  };

  const handleOnSort = ({ sortBy, sortOrder }) => {
    setSortParameters({ order: sortOrder, sort: sortBy });
    setQueryParameters({
      ...queryParameters,
      pageNumber: 1,
      pageSize: queryParameters.pageSize,
      sortBy,
      sortOrder,
    });
  };

  const handleOnPaginate = (pageNumber, pageSize) => {
    setQueryParameters({ ...queryParameters, pageNumber, pageSize });
  };

  useEffect(() => {
    if(prioritisation.channelId && prioritisation.month && prioritisation.year){
      handleGetTitles(false);
    }
   // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [queryParameters]);

  const handleMultiSelect = (e) => {
    selectTitleGroups(e);
  };

  return (
    <>
      <div class="col-12" style={{ display: 'flex', flexWrap: 'wrap' }}>
        <div className={`${Styles.tableLabel}`}>Available title groups:</div>

        <div className="d-flex">
          <Table
            noDataVerbiage="No available title group."
            headers={tableHeaders}
            list={groups}
            isFetchingData={isLoading}
            queryParameters={tableParameters}
            paginationParameters={paginationParameters}
            sortParameters={sortParameters}
            onFilter={handleOnFilter}
            onClearFilters={handleOnClearFilter}
            onSort={handleOnSort}
            onPaginate={handleOnPaginate}
            onDoubleClickSelectData={null}
            onSingleClickSelectData={null}
            tableHeight={tableHeight}
            filter={null}
            onAction={handleOnAction}
            defaultSelected={null}
            onMultiSelect={handleMultiSelect}
            clearSelected={clearSelected}
            setClearSelected={(e) => setClearSelected(e)}
          />
        </div>
      </div>
    </>
  );
}

export default AvailableTitleGroupComponent;
