import React, { useState, useEffect, useRef } from 'react';
import PropTypes from 'prop-types';
import styles from './starts.with.filter.module.css';

const { forwardRef, useImperativeHandle } = React;
const StartsWithFilterComponent = forwardRef((props, ref) => {
  const options = [
    {
      optionId: 1,
      optionText: 'Starts With',
    },
    {
      optionId: 2,
      optionText: 'Ends With',
    },
    {
      optionId: 3,
      optionText: 'Contains',
    },
    {
      optionId: 4,
      optionText: 'Equal',
    },
    {
      optionId: 5,
      optionText: 'Not Equal',
    },
  ];
  const [selectedOption, setSelectedOption] = useState(options[0]);
  const [showOptions, setShowOptions] = useState(false);
  const [startsWithInput, setStartsWithInput] = useState('');
  const [disableFilterButton, setDisableFilterButton] = useState(true);
  const startsWithFilterRef = useRef();

  const backdropClicked = (e) => {
    if (!startsWithFilterRef.current.contains(e.target)) {
      props.backdropClicked(props.component);
      setShowOptions(false);
    }
  };

  const toggleOptions = () => {
    setShowOptions(!showOptions);
  };

  const selectOption = (option) => {
    setSelectedOption(option);
    setShowOptions(false);
    if (startsWithInput !== '') {
      setDisableFilterButton(false);
    }
  };

  const bindInput = (e) => {
    setDisableFilterButton(false);

    setStartsWithInput(e.target.value);
    if (selectedOption.optionId === options[0].optionId) {
      selectOption(options[0]);
    }
  };

  const changeToDefaultForm = () => {
    setSelectedOption(options[0]);
    setStartsWithInput('');
    setDisableFilterButton(true);
  };

  const setTemporaryFilterModel = (action) => {
    const model = JSON.parse(JSON.stringify(props.filterModel));
    if (action === 'Filter') {
      if (props.component === 'Title Name') {
        model.titleFilter = selectedOption.optionId;
        model.titleFilterValue = startsWithInput;
        model.useCache = true;
      }
      if (props.component === 'Genre') {
        model.genreFilter = selectedOption.optionId;
        model.genreFilterValue = startsWithInput;
        model.useCache = true;
      }
      if (props.component === 'Duration') {
        model.durationFilter = selectedOption.optionId;
        model.durationFilterValue = startsWithInput;
        model.useCache = true;
      }
      if (props.component === 'Grade') {
        model.gradeFilter = selectedOption.optionId;
        model.gradeFilterValue = startsWithInput;
        model.useCache = true;
      }
      if (props.component === 'Year') {
        model.yearFilter = selectedOption.optionId;
        model.yearFilterValue = startsWithInput;
        model.useCache = true;
      }
      if (props.component === 'Certificate') {
        model.certificateFilter = selectedOption.optionId;
        model.certificateFilterValue = startsWithInput;
        model.useCache = true;
      }
    } else {
      if (props.component === 'Title Name') {
        model.titleFilter = undefined;
        model.titleFilterValue = undefined;
        model.useCache = true;
      }
      if (props.component === 'Genre') {
        model.genreFilter = undefined;
        model.genreFilterValue = undefined;
        model.useCache = true;
      }
      if (props.component === 'Duration') {
        model.durationFilter = undefined;
        model.durationFilterValue = undefined;
        model.useCache = true;
      }
      if (props.component === 'Grade') {
        model.gradeFilter = undefined;
        model.gradeFilterValue = undefined;
        model.useCache = true;
      }
      if (props.component === 'Year') {
        model.yearFilter = undefined;
        model.yearFilterValue = undefined;
        model.useCache = true;
      }
      if (props.component === 'Certificate') {
        model.certificateFilter = undefined;
        model.certificateFilterValue = undefined;
        model.useCache = true;
      }
    }

    return model;
  };

  const onClickedButtonFilter = () => {
    props.onClickedButtonFilter(setTemporaryFilterModel('Filter'));
    props.toggleDropdown(null);
    setDisableFilterButton(true);
  };

  const onClickedButtonClearFilter = () => {
    changeToDefaultForm();
    props.onClickedButtonFilter(setTemporaryFilterModel('Clear'));
    props.toggleDropdown(null);
  };

  useImperativeHandle(ref, () => ({
    clearFilterModel() {
      changeToDefaultForm();
    },
  }));

  useEffect(() => {
    document.addEventListener('mousedown', backdropClicked);
    return () => document.removeEventListener('mousedown', backdropClicked);
  });

  return (
    <div className="p-3" ref={startsWithFilterRef}>
      <div />
      <div className="position-relative">
        <button
          className={styles.startsWithButton}
          onClick={toggleOptions}
          type="button"
        >
          {selectedOption.optionText}
        </button>
        <div
          className={styles.options}
          style={{
            display: showOptions ? null : 'none',
            marginBottom: `-${25 * options.length}px`,
            height: '127px',
          }}
        >
          {options.map((value) => (
            <div
              className={styles.optionItem}
              key={value.id}
              onClick={() => selectOption(value)}
              onKeyDown={() => selectOption(value)}
              role="presentation"
            >
              <span>{value.optionText}</span>
            </div>
          ))}
        </div>
      </div>
      <br />
      <input
        className={styles.startsWithInput}
        type="text"
        value={startsWithInput}
        placeholder="Enter the value"
        onChange={bindInput}
      />
      <hr style={{ backgroundColor: '#6d6d73' }} />
      <div className="d-flex">
        <p
          className={styles.clearFilterButton}
          onClick={onClickedButtonClearFilter}
          onKeyDown={onClickedButtonClearFilter}
          role="presentation"
        >
          Clear
        </p>
        <button
          className={styles.filterButton}
          onClick={onClickedButtonFilter}
          disabled={disableFilterButton}
          type="button"
        >
          Filter
        </button>
      </div>
    </div>
  );
});
StartsWithFilterComponent.propTypes = {
  backdropClicked: PropTypes.func.isRequired,
  component: PropTypes.string,
  onClickedButtonFilter: PropTypes.func.isRequired,
  toggleDropdown: PropTypes.func.isRequired,
  filterModel: PropTypes.shape.isRequired,
};
StartsWithFilterComponent.defaultProps = {
  component: '',
};
StartsWithFilterComponent.displayName = 'StartsWithFilterComponent';
export default StartsWithFilterComponent;
