import React, { useState, useRef, useEffect } from 'react';
import PropTypes from 'prop-types';
import styles from './AvailableProgramsTableHeader.module.css';
import filterIcon from '../../../../assets/icons/Filter.png';
import filteredIcon from '../../../../assets/icons/Filtered.png';
import arrowUpIcon from '../../../../assets/icons/Arrow-Up.png';
import clearFilterIcon from '../../../../assets/icons/Clear-Filter.svg';

import PrioritisationForms from '../../PrioritisationForms';

import StartsWithFilterComponent from './filters/starts-with-filter/starts.with.filter.component';

function AvailableProgramsHeaderComponent(props) {
  const {
    filterModel,
    onClickedButtonFilter,
    sortBy,
    handleSetSortBy,
    onClickedButtonClearAllFilters,
    titleSource,
    onClickedButtonClearFilter,
  } = props;
  const tableHeaders = PrioritisationForms.availableProgramsHeader;
  const [showTitleNameFilter, setShowTitleNameFilter] = useState(false);
  const [showGenreFilter, setShowGenreFilter] = useState(false);
  const [showDurationFilter, setShowDurationFilter] = useState(false);
  const [showGradeFilter, setShowGradeFilter] = useState(false);
  const [showYearFilter, setShowYearFilter] = useState(false);
  const [showCertificateFilter, setShowCertificateFilter] = useState(false);

  const [showClearFilterIcon, setShowClearAllFilterIcon] = useState(false);
  const titleNameFilterRef = useRef();
  const genreFilterRef = useRef();
  const durationFilterRef = useRef();
  const gradeFilterRef = useRef();
  const yearFilterRef = useRef();
  const certificateFilterRef = useRef();

  const toggleDropdown = (header) => {
    if (header === tableHeaders[0]) {
      setShowTitleNameFilter(!showTitleNameFilter);
    } else {
      setShowTitleNameFilter(false);
    }

    if (header === tableHeaders[1]) {
      setShowGenreFilter(!showGenreFilter);
    } else {
      setShowGenreFilter(false);
    }

    if (header === tableHeaders[2]) {
      setShowDurationFilter(!showDurationFilter);
    } else {
      setShowDurationFilter(false);
    }

    if (header === tableHeaders[3]) {
      setShowGradeFilter(!showGradeFilter);
    } else {
      setShowGradeFilter(false);
    }

    if (header === tableHeaders[4]) {
      setShowCertificateFilter(!showCertificateFilter);
    } else {
      setShowCertificateFilter(false);
    }

    if (header === tableHeaders[5]) {
      setShowYearFilter(!showYearFilter);
    } else {
      setShowYearFilter(false);
    }
  };

  const backdropClicked = (header) => {
    switch (header) {
      case tableHeaders[0]:
        setShowTitleNameFilter(false);
        break;
      case tableHeaders[1]:
        setShowGenreFilter(false);
        break;
      case tableHeaders[2]:
        setShowDurationFilter(false);
        break;
      case tableHeaders[3]:
        setShowGradeFilter(false);
        break;
      case tableHeaders[4]:
        setShowCertificateFilter(false);
        break;
      case tableHeaders[5]:
        setShowYearFilter(false);
        break;
      default:
        break;
    }
  };

  const onClickedButtonFilterHeader = (model) => {
    onClickedButtonFilter(model);
  };

  const handleSort = (id) => {
    const model = JSON.parse(JSON.stringify(filterModel));

    model.userSort = id;
    model.sortBy = sortBy[id].state ? 2 : 1;

    onClickedButtonFilterHeader(model);
    handleSetSortBy(id);
  };

  const onClickedButtonClearAllFiltersHeader = () => {
    titleNameFilterRef.current.clearFilterModel();
    genreFilterRef.current.clearFilterModel();
    durationFilterRef.current.clearFilterModel();
    gradeFilterRef.current.clearFilterModel();
    certificateFilterRef.current.clearFilterModel();
    yearFilterRef.current.clearFilterModel();

    onClickedButtonClearAllFilters();
  };

  const handleClearFilterIcon = () => {
    setShowClearAllFilterIcon(
      (
        filterModel.titleFilter
        || filterModel.titleFilterValue
        || filterModel.genreFilter
        || filterModel.genreFilterValue
        // props.filterModel.durationFilter ||
        // props.filterModel.durationFilterValue ||
        || filterModel.gradeFilter
        || filterModel.gradeFilterValue
        || filterModel.certificateFilter
        || filterModel.certificateFilterValue
        || filterModel.yearFilter
        || filterModel.yearFilterValue
      ),
    );
  };

  useEffect(() => {
    handleClearFilterIcon();
    // eslint-disable-next-line
  }, [filterModel]);

  return (
    <tr>
      <td className={styles.columns}>
        <span
          onClick={() => handleSort(1)}
          onKeyDown={() => handleSort(1)}
          role="presentation"
        >
          {tableHeaders[0]}
        </span>
        {' '}
        <img
          className={`${styles.filterIcons} mx-2`}
          src={
            filterModel.titleFilter && filterModel.titleFilterValue
              ? filteredIcon
              : filterIcon
          }
          alt="FilterIcon1"
          onClick={() => toggleDropdown(`${tableHeaders[0]}`)}
          onKeyDown={() => toggleDropdown(`${tableHeaders[0]}`)}
          role="presentation"
        />
        <div
          className={styles.dropdownFilter}
          style={{ display: showTitleNameFilter ? null : 'none' }}
        >
          <StartsWithFilterComponent
            ref={titleNameFilterRef}
            component={tableHeaders[0]}
            filterModel={filterModel}
            onClickedButtonFilter={onClickedButtonFilterHeader}
            onClickedButtonClearFilter={() => onClickedButtonClearFilter(`${tableHeaders[0]}`)}
            toggleDropdown={toggleDropdown}
            backdropClicked={backdropClicked}
          />
        </div>
        <img
          src={arrowUpIcon}
          alt="ArrowUpIcon1"
          style={{
            display:
              filterModel.userSort === 1 || filterModel.userSort === undefined
                ? null
                : 'none',
            transform: sortBy[1].state ? 'rotate(0deg)' : 'rotate(180deg)',
          }}
        />
      </td>
      {titleSource === 1 && (
        <td className={styles.columns}>
          <span
            onClick={() => handleSort(2)}
            onKeyDown={() => handleSort(2)}
            role="presentation"
          >
            {tableHeaders[1]}
          </span>
          {' '}
          <img
            className={`${styles.filterIcons} mx-2`}
            src={
              filterModel.genreFilter && filterModel.genreFilterValue
                ? filteredIcon
                : filterIcon
            }
            alt="FilterIcon2"
            onClick={() => toggleDropdown(`${tableHeaders[1]}`)}
            onKeyDown={() => toggleDropdown(`${tableHeaders[1]}`)}
            role="presentation"
          />
          <div
            className={styles.dropdownFilter}
            style={{ display: showGenreFilter ? null : 'none' }}
          >
            <StartsWithFilterComponent
              ref={genreFilterRef}
              component={tableHeaders[1]}
              filterModel={filterModel}
              onClickedButtonFilter={onClickedButtonFilterHeader}
              onClickedButtonClearFilter={() => onClickedButtonClearFilter(`${tableHeaders[1]}`)}
              toggleDropdown={toggleDropdown}
              backdropClicked={backdropClicked}
            />
          </div>
          <img
            src={arrowUpIcon}
            alt="ArrowUpIcon2"
            style={{
              display: filterModel.userSort === 2 ? null : 'none',
              transform: sortBy[2].state ? 'rotate(0deg)' : 'rotate(180deg)',
            }}
          />
        </td>
      )}
      <td className={styles.columns}>
        <span
          onClick={() => handleSort(3)}
          onKeyDown={() => handleSort(3)}
          role="presentation"
        >
          {tableHeaders[2]}
        </span>
        {' '}
        <div
          className={styles.dropdownFilter}
          style={{ display: showDurationFilter ? null : 'none' }}
        >
          <StartsWithFilterComponent
            ref={durationFilterRef}
            component={tableHeaders[2]}
            filterModel={filterModel}
            onClickedButtonFilter={onClickedButtonFilterHeader}
            onClickedButtonClearFilter={() => onClickedButtonClearFilter(`${tableHeaders[2]}`)}
            toggleDropdown={toggleDropdown}
            backdropClicked={backdropClicked}
          />
        </div>
        <img
          src={arrowUpIcon}
          alt="ArrowUpIcon3"
          style={{
            display: filterModel.userSort === 3 ? null : 'none',
            transform: sortBy[3].state ? 'rotate(0deg)' : 'rotate(180deg)',
          }}
        />
      </td>
      {titleSource === 1 && (
        <td className={styles.columns}>
          <span
            onClick={() => handleSort(4)}
            onKeyDown={() => handleSort(4)}
            role="presentation"
          >
            {tableHeaders[3]}
          </span>
          {' '}
          <img
            className={`${styles.filterIcons} mx-2`}
            src={
              filterModel.gradeFilter && filterModel.gradeFilterValue
                ? filteredIcon
                : filterIcon
            }
            alt="FilterIcon3"
            onClick={() => toggleDropdown(`${tableHeaders[3]}`)}
            onKeyDown={() => toggleDropdown(`${tableHeaders[3]}`)}
            role="presentation"
          />
          <div
            className={styles.dropdownFilter}
            style={{ display: showGradeFilter ? null : 'none' }}
          >
            <StartsWithFilterComponent
              ref={gradeFilterRef}
              component={tableHeaders[3]}
              filterModel={filterModel}
              onClickedButtonFilter={onClickedButtonFilterHeader}
              onClickedButtonClearFilter={() => onClickedButtonClearFilter(`${tableHeaders[3]}`)}
              toggleDropdown={toggleDropdown}
              backdropClicked={backdropClicked}
            />
          </div>
          <img
            src={arrowUpIcon}
            alt="ArrowUpIcon4"
            style={{
              display: filterModel.userSort === 4 ? null : 'none',
              transform: sortBy[4].state ? 'rotate(0deg)' : 'rotate(180deg)',
            }}
          />
        </td>
      )}
      {titleSource === 1 && (
        <td className={styles.columns}>
          <span
            onClick={() => handleSort(5)}
            onKeyDown={() => handleSort(5)}
            role="presentation"
          >
            {tableHeaders[4]}
          </span>
          {' '}
          <img
            className={`${styles.filterIcons} mx-1`}
            src={
              filterModel.certificateFilter
              && filterModel.certificateFilterValue
                ? filteredIcon
                : filterIcon
            }
            alt="FilterIcon5"
            onClick={() => toggleDropdown(`${tableHeaders[4]}`)}
            onKeyDown={() => toggleDropdown(`${tableHeaders[4]}`)}
            role="presentation"
            backdropClicked={backdropClicked}
          />
          <div
            className={styles.dropdownFilter}
            style={{ display: showCertificateFilter ? null : 'none' }}
          >
            <StartsWithFilterComponent
              ref={certificateFilterRef}
              component={tableHeaders[4]}
              filterModel={filterModel}
              onClickedButtonFilter={onClickedButtonFilterHeader}
              onClickedButtonClearFilter={() => onClickedButtonClearFilter(`${tableHeaders[4]}`)}
              toggleDropdown={toggleDropdown}
              backdropClicked={backdropClicked}
            />
          </div>
          <img
            src={arrowUpIcon}
            alt="ArrowUpIcon5"
            style={{
              display: filterModel.userSort === 5 ? null : 'none',
              transform: sortBy[5].state ? 'rotate(0deg)' : 'rotate(180deg)',
            }}
          />
        </td>
      )}

      {titleSource === 1 && (
        <td className={styles.columns}>
          <span
            onClick={() => handleSort(6)}
            onKeyDown={() => handleSort(6)}
            role="presentation"
          >
            {tableHeaders[5]}
          </span>
          {' '}
          <img
            className={`${styles.filterIcons} mx-1`}
            src={
              filterModel.yearFilter && filterModel.yearFilterValue
                ? filteredIcon
                : filterIcon
            }
            alt="FilterIcon6"
            onClick={() => toggleDropdown(`${tableHeaders[5]}`)}
            onKeyDown={() => toggleDropdown(`${tableHeaders[5]}`)}
            role="presentation"
            backdropClicked={backdropClicked}
          />
          <div
            className={styles.dropdownFilter}
            style={{ display: showYearFilter ? null : 'none' }}
          >
            <StartsWithFilterComponent
              ref={yearFilterRef}
              component={tableHeaders[5]}
              filterModel={filterModel}
              onClickedButtonFilter={onClickedButtonFilter}
              onClickedButtonClearFilter={() => onClickedButtonClearFilter(`${tableHeaders[5]}`)}
              toggleDropdown={toggleDropdown}
              backdropClicked={backdropClicked}
            />
          </div>
          <img
            src={arrowUpIcon}
            alt="ArrowUpIcon6"
            style={{
              display: filterModel.userSort === 6 ? null : 'none',
              transform: sortBy[6].state ? 'rotate(0deg)' : 'rotate(180deg)',
            }}
          />
          <img
            className={`${styles.clearFilterIcons}`}
            style={{ display: showClearFilterIcon ? null : 'none' }}
            src={clearFilterIcon}
            alt="ClearFilterIcon"
            onClick={onClickedButtonClearAllFiltersHeader}
            onKeyDown={onClickedButtonClearAllFiltersHeader}
            role="presentation"
          />
        </td>
      )}
      <td
        className={styles.columns}
        style={{ width: titleSource === 2 ? '135px' : '30px' }}
      >
        {titleSource === 2 && 'Segments'}
      </td>
    </tr>
  );
}
AvailableProgramsHeaderComponent.propTypes = {
  filterModel: PropTypes.shape.isRequired,
  onClickedButtonFilter: PropTypes.func.isRequired,
  sortBy: PropTypes.arrayOf(PropTypes.shape).isRequired,
  handleSetSortBy: PropTypes.func.isRequired,
  onClickedButtonClearAllFilters: PropTypes.func.isRequired,
  titleSource: PropTypes.number.isRequired,
  onClickedButtonClearFilter: PropTypes.func.isRequired,
};
export default AvailableProgramsHeaderComponent;
