import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import Styles from './Tabs.module.css';
import TabHeaderComponent from './TabHeaderComponent';

function TabsComponent(props) {
  const {
    activeTab,
    labelList,
    customClass,
    dirty,
    tabChange,
    setActive,
    onSetSelectedTab,
    customClassActiveHeader,
  } = props;
  const [activeTabComponent, setActiveTab] = useState('');

  const onClickTabItem = (label) => {
    if (dirty) {
      tabChange(label);
    } else {
      setActive && setActive(label);
      !setActive && setActiveTab(label);
    }
    if (typeof onSetSelectedTab === 'function') onSetSelectedTab(label);
  };

  useEffect(() => {
    setActiveTab(activeTab);
    if (typeof onSetSelectedTab === 'function') onSetSelectedTab(activeTab);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [activeTab]);

  return (
    <div className="tabs">
      <ol className={Styles.tabList}>
        {labelList.map((child) => (
          <TabHeaderComponent
            key={child.id}
            activeTab={activeTabComponent}
            label={child.label}
            onClick={onClickTabItem}
            customClassActiveHeader={customClassActiveHeader}
          />
        ))}
      </ol>
      <div className={`${'tab-content'} ${Styles.tabContent} ${customClass}`}>
        {labelList.map((child) => {
          if (activeTabComponent === child.label) return child.component;
          return undefined;
        })}
      </div>
    </div>
  );
}
TabsComponent.propTypes = {
  activeTab: PropTypes.string,
  labelList: PropTypes.arrayOf(PropTypes.shape),
  height: PropTypes.string,
};
TabsComponent.defaultProps = {
  activeTab: null,
  labelList: null,
  height: null,
};
export default TabsComponent;
