import React, {
  forwardRef,
  useImperativeHandle,
  useState,
  useRef,
} from 'react';
import PropTypes from 'prop-types';
import TableComponent from '../../_reusable/table/TableComponent';
import PrioritisedProgramsDataComponent from './PrioritisedProgramsDataComponent';
import PrioritisationForms from '../PrioritisationForms';
import Styles from '../Prioritisation.module.css';
import PrioritisedProgramsHeaderComponent from './table-header/PrioritisedProgramsHeaderComponent';

const PrioritisedProgramsComponent = forwardRef(
  (
    {
      programs,
      callbackViewProgram,
      handleSelectProgram,
      paginationParameters,
      paginate,
      selectedPrograms,
      handleSortPrioritised,
      tableMinimize,
      callbackViewSegment,
      titleSource,
      viewedProgram,
      isLoading,
    },
    ref,
  ) => {
    const dataComponentRef = useRef();
    const tableHeaderColumns = PrioritisationForms.tableHeaders;
    const [filterModel, setFilterModel] = useState({});

    const [sortInfo, setSortInfo] = useState({});
    const [sortBy, setSortBy] = useState([
      {},
      {
        state: true,
      },
      {
        state: false,
      },
    ]);

    useImperativeHandle(ref, () => ({
      scrollToProgram() {
        dataComponentRef.current.scrollToProgram();
      },
    }));

    const handleSetSortBy = (id) => {
      const newArr = sortBy;

      if (newArr[id].state === true) {
        newArr[id].state = false;
      } else {
        newArr[id].state = true;
      }

      setSortBy(newArr);
      setSortInfo({
        userSort: id,
        sortBy: newArr[id].state ? 1 : 2,
      });
    };

    const onClickedButtonFilter = (model) => {
      handleSortPrioritised(model);
    };

    const onClickedButtonClearAllFilters = () => {
      setFilterModel({
        userSort: sortInfo.userSort,
        sortBy: sortInfo.sortBy,
        useCache: true,
      });
    };

    const handleSort = () => {};

    return (
      <div>
        <TableComponent
          component={(
            <PrioritisedProgramsDataComponent
              ref={dataComponentRef}
              programs={programs}
              callbackViewProgram={callbackViewProgram}
              callbackSelectPrograms={handleSelectProgram}
              selectedPrograms={selectedPrograms}
              tableMinimize={tableMinimize}
              callbackViewSegment={callbackViewSegment}
              titleSource={titleSource}
              viewedProgram={viewedProgram}
              isLoading={isLoading}
            />
          )}
          tableHeaderColumns={tableHeaderColumns}
          paginationParameters={paginationParameters}
          paginate={paginate}
          sortHeader={handleSort}
          hasExport={false}
          stylesTable={
            !tableMinimize
              ? Styles.prioritizeTableBodyDiv
              : Styles.prioritizeTableBodyDivSmall
          }
          tableHeader={(
            <PrioritisedProgramsHeaderComponent
              onClickedButtonClearAllFilters={onClickedButtonClearAllFilters}
              filterModel={filterModel}
              sortBy={sortBy}
              handleSetSortBy={handleSetSortBy}
              onClickedButtonFilter={onClickedButtonFilter}
            />
          )}
          isTableBodyLoading={isLoading}
        />
      </div>
    );
  },
);
PrioritisedProgramsComponent.propTypes = {
  programs: PropTypes.arrayOf(PropTypes.shape),
  callbackViewProgram: PropTypes.func.isRequired,
  handleSelectProgram: PropTypes.func.isRequired,
  paginationParameters: PropTypes.shape.isRequired,
  paginate: PropTypes.func.isRequired,
  selectedPrograms: PropTypes.arrayOf(PropTypes.number),
  handleSortPrioritised: PropTypes.func.isRequired,
  tableMinimize: PropTypes.bool,
  callbackViewSegment: PropTypes.func.isRequired,
  titleSource: PropTypes.number.isRequired,
  viewedProgram: PropTypes.number,
  isLoading: PropTypes.bool,
};
PrioritisedProgramsComponent.defaultProps = {
  programs: null,
  selectedPrograms: null,
  tableMinimize: false,
  viewedProgram: null,
  isLoading: true,
};
PrioritisedProgramsComponent.displayName = 'PrioritisedProgramsComponent';
export default PrioritisedProgramsComponent;
