import React, {
  forwardRef,
  useImperativeHandle,
  useState,
  useRef,
} from 'react';
import PropTypes from 'prop-types';
import TableComponent from '../../_reusable/table/TableComponent';
import AvailableProgramsDataComponent from './AvailableProgramsDataComponent';
import PrioritisationForms from '../PrioritisationForms';
import Styles from '../Prioritisation.module.css';
import AvailableProgramsHeaderComponent from './table-header/AvailableProgramsHeaderComponent';

const AvailableProgramsComponent = forwardRef(
  (
    {
      programs,
      callbackViewProgram,
      handleSelectProgram,
      isLoading,
      availablePaginationParameters,
      paginate,
      selectedPrograms,
      setFilterModel,
      filterModel,
      // selectedChannel,
      callbackViewSegment,
      tableMinimize,
      titleSource,
      viewedProgram,
    },
    ref,
  ) => {
    const dataComponentRef = useRef();
    const tableHeaderColumns = PrioritisationForms.tableHeaders;
    const [sortInfo, setSortInfo] = useState({});
    const [sortBy, setSortBy] = useState([
      {},
      {
        state: true,
      },
      {
        state: false,
      },
      {
        state: false,
      },
      {
        state: false,
      },
      {
        state: false,
      },
      {
        state: false,
      },
    ]);

    useImperativeHandle(ref, () => ({
      scrollToProgram() {
        dataComponentRef.current.scrollToProgram();
      },
    }));

    const handleSetSortBy = (id) => {
      const newArr = sortBy;
      if (newArr[id].state === true) {
        newArr[id].state = false;
      } else {
        newArr[id].state = true;
      }

      setSortBy(newArr);
      setSortInfo({
        userSort: id,
        sortBy: newArr[id].state ? 1 : 2,
      });
    };

    const onClickedButtonFilter = (model) => {
      setFilterModel(model);
    };

    const onClickedButtonClearAllFilters = () => {
      setFilterModel({
        userSort: sortInfo.userSort,
        sortBy: sortInfo.sortBy,
        useCache: true,
      });
    };

    const handleSort = () => {};

    return (
      <div>
        <TableComponent
          component={(
            <AvailableProgramsDataComponent
              ref={dataComponentRef}
              programs={programs}
              callbackSelectPrograms={handleSelectProgram}
              callbackViewProgram={callbackViewProgram}
              isLoading={isLoading}
              selectedPrograms={selectedPrograms}
              // selectedChannel={selectedChannel}
              // viewProgram={viewProgram}
              callbackViewSegment={callbackViewSegment}
              viewedProgram={viewedProgram}
              tableMinimize={tableMinimize}
              titleSource={titleSource}
            />
          )}
          tableHeaderColumns={tableHeaderColumns}
          paginationParameters={availablePaginationParameters}
          // onExport={handleExport}
          sortHeader={handleSort}
          // isExporting={isExporting}
          hasExport={false}
          stylesTable={
            !tableMinimize ? Styles.tableBodyDiv : Styles.tableBodyDivSmall
          }
          tableHeader={(
            <AvailableProgramsHeaderComponent
              onClickedButtonClearAllFilters={onClickedButtonClearAllFilters}
              filterModel={filterModel}
              sortBy={sortBy}
              handleSetSortBy={handleSetSortBy}
              onClickedButtonFilter={onClickedButtonFilter}
              titleSource={titleSource}
            />
          )}
          paginate={paginate}
          isTableBodyLoading={isLoading}
        />
      </div>
    );
  },
);
AvailableProgramsComponent.propTypes = {
  programs: PropTypes.arrayOf(PropTypes.shape),
  callbackViewProgram: PropTypes.func.isRequired,
  handleSelectProgram: PropTypes.func.isRequired,
  isLoading: PropTypes.bool,
  availablePaginationParameters: PropTypes.func.isRequired,
  paginate: PropTypes.func.isRequired,
  selectedPrograms: PropTypes.arrayOf(PropTypes.number),
  setFilterModel: PropTypes.func.isRequired,
  filterModel: PropTypes.shape.isRequired,
  // selectedChannel: PropTypes.shape,
  callbackViewSegment: PropTypes.func.isRequired,
  tableMinimize: PropTypes.bool,
  titleSource: PropTypes.number.isRequired,
  viewedProgram: PropTypes.number,
};
AvailableProgramsComponent.defaultProps = {
  programs: null,
  isLoading: true,
  selectedPrograms: null,
  // selectedChannel: null,
  tableMinimize: PropTypes.bool,
  viewedProgram: null,
};
AvailableProgramsComponent.displayName = 'AvailableProgramsComponent';
export default AvailableProgramsComponent;
