import axios from 'axios';

const ProfileService = {
  getUser(id) {
    const url = `${process.env.REACT_APP_HOST_URL}user/${id}`;
    return axios.get(url);
  },

  updateUser(userId, json) {
    const url = `${process.env.REACT_APP_HOST_URL}user/${userId}`;
    return axios.put(url, json);
  },

  changePassword(id, json) {
    const url = `${process.env.REACT_APP_HOST_URL}user/changepassword/${id}`;
    return axios.patch(url, json);
  },
};
export default ProfileService;
