import React, { useEffect, useRef, useState } from 'react';
import PropTypes from 'prop-types';

import styles from './styles.module.css';

function SelectComponentCustom({
  label,
  text,
  list,
  onSelect,
  width,
  disabled,
}) {
  const optionRef = useRef();
  const [showOptionList, setShowOptionList] = useState(false);
  const [disabledSelect, setDisabledSelect] = useState(false);

  useEffect(() => {
    const backdropClicked = (e) => {
      if (optionRef.current && !optionRef.current.contains(e.target)) setShowOptionList(false);
    };

    document.addEventListener('mousedown', backdropClicked);
    return () => document.removeEventListener('mousedown', backdropClicked);
  }, [optionRef]);

  useEffect(() => {
    setDisabledSelect(disabled);
  }, [disabled]);

  const toggleOptionList = () => {
    setShowOptionList(true);
  };

  const selectItem = (value) => {
    if (typeof onSelect === 'function') {
      onSelect(value);
      setShowOptionList(false);
    }
  };

  return (
    <div className={styles.select}>
      {label && <div className={styles.label}>{label}</div>}

      {text && (
        <button
          className={styles.selectButton}
          style={{ width }}
          title={text}
          onClick={toggleOptionList}
          disabled={disabledSelect}
          type="button"
        >
          <span>{text}</span>
        </button>
      )}

      {showOptionList && (
        <div ref={optionRef} className={styles.optionList} style={{ width }}>
          {list
            && list.map((value) => (
              <div
                key={value.id}
                className={styles.optionItem}
                title={value.value}
                onClick={() => selectItem(value)}
                onKeyDown={() => selectItem(value)}
                role="presentation"
              >
                {value.value}
              </div>
            ))}
        </div>
      )}
    </div>
  );
}

SelectComponentCustom.propTypes = {
  label: PropTypes.string,
  text: PropTypes.string.isRequired,
  list: PropTypes.arrayOf(PropTypes.object),
  onSelect: PropTypes.func.isRequired,
  width: PropTypes.string,
  disabled: PropTypes.bool,
};

SelectComponentCustom.defaultProps = {
  label: '',
  list: null,
  width: '70px',
  disabled: false,
};

export default SelectComponentCustom;
