import React, { useEffect, useState } from 'react';
import PropTypes from 'prop-types';
import Styles from './Tabs.module.css';

function TabHeaderComponent(props) {
  const { activeTab, onClick, label, customClassActiveHeader } = props;
  const [className, setClassName] = useState('');

  const handleOnClick = (labelName) => {
    onClick(labelName);
  };

  useEffect(() => {
    if (activeTab === label) {
      setClassName(
        `${Styles.tabListItem} ${Styles.tabListActive} ${customClassActiveHeader}`
      );
    } else {
      setClassName(Styles.tabListItem);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [activeTab]);

  return (
    <li
      className={`${className} ${Styles.tabHeader}`}
      onClick={() => {
        handleOnClick(label);
      }}
      onKeyDown={() => {
        handleOnClick(label);
      }}
      role="presentation"
    >
      {label}
    </li>
  );
}
TabHeaderComponent.propTypes = {
  activeTab: PropTypes.string,
  onClick: PropTypes.func.isRequired,
  label: PropTypes.string,
};
TabHeaderComponent.defaultProps = {
  activeTab: null,
  label: null,
};
export default TabHeaderComponent;
