import React, { useState, useEffect, useRef } from 'react';
import Styles from '../Prioritisation.module.css';
import AvailableTitleGroupComponent from './available-title-groups/AvailableTitleGroupComponent';
import PrioritisedTitleGroupComponent from './prioritised-title-groups/PrioritisedTitleGroupComponent';
import TitleGroupDetailsComponent from './TitleGroupDetailsComponent';
import { arrowLeftIcon, arrowRightIcon } from '../../../assets/index';
import Button from '../../../shared/components/button/Button';
import PrioritisationService from '../PrioritisationService';
import {
  success,
  danger,
} from '../../../shared/elements/notification/NotifyComponent';
import { eyeIcon, eyeSlashIcon } from '../../../assets/index';
import { TitleGroupStorage } from './TitleGroupStorage';
export default function TitleGroupComponent({
  prioritisation,
  changed,
  setChanged,
  discardTitleGroupDirty,
  setDiscardTitleGroupDirty,
  saveTitleGroupDirty,
  setSaveTitleGroupDirty,
}) {
  const [allGroups, setAllGroups] = useState([]);
  const [groups, setGroups] = useState([]);
  const [allAvailableGroups, setAllAvailableGroups] = useState([]);
  const [paginationParameters, setPaginationParameters] = useState(
    TitleGroupStorage.pagination
  );
  const [clearSelectedGroups, setClearSelectedGroups] = useState(false);
  const [selectedGroups, setSelectedGroups] = useState([]);
  const [prioritisedGroups, setPrioritisedGroups] = useState([]);
  const [allPrioritisedGroups, setAllPrioritisedGroups] = useState([]);
  const [clearSelectedPrioritise, setClearSelectedPrioritise] = useState(false);
  const [selectedPrioritisedGroups, setSelectedPrioritisedGroups] = useState(
    []
  );
  const [selectedViewId, setSelectedViewId] = useState();
  const [viewGroupDetails, setViewGroupDetails] = useState();
  const [
    paginationParametersPrioritised,
    setPaginationParametersPrioritised,
  ] = useState(TitleGroupStorage.pagination);
  const [isSaving, setIsSaving] = useState(false);
  const [isLoading, setIsLoading] = useState(false);
  const [queryParameters, setQueryParameters] = useState({
    pageNumber: 1,
    pageSize: 50,
  });
  const [prioritisedQueryParameters, setPrioritisedQueryParameters] = useState({
    pageNumber: 1,
    pageSize: 50,
  });
  const [sortParameters, setSortParameters] = useState({ order: 1, sort: 1 });
  const [sortPrioritiseParameters, setSortPrioritiseParameters] = useState({
    order: 1,
    sort: 1,
  });

  const constructQueryParameters = (parameters) => {
    const pagination = `?pageNumber=${
      parameters.pageNumber ? parameters.pageNumber : 1
    }
    &pageSize=${parameters.pageSize ? parameters.pageSize : 50}`;

    const title =
      parameters.titleGroupFilter && parameters.titleGroupFilterValue
        ? `&titleGroupFilter=${parameters.titleGroupFilter}&titleGroupFilterValue=${parameters.titleGroupFilterValue}`
        : '';

    let titlesCount =
      parameters.titlesCountFilter && parameters.titlesCountFilterValue
        ? `&titlesCountFilter=${parameters.titlesCountFilter}&titlesCountFilterValue=${parameters.titlesCountFilterValue}`
        : '';

    const sort =
      parameters.sortOrder && parameters.sortBy
        ? `&sort=${parameters.sortBy}&sortBy=${parameters.sortOrder}`
        : '';

    let searchKey = parameters.searchKey
      ? `&SearchKey=${parameters.searchKey}`
      : '';
    if (parameters.searchKey != null && parameters.searchKey.includes('+')) {
      searchKey = searchKey.replace('+', '%2B');
    }
    const useCache = parameters.useCache
      ? `&useCache=${parameters.useCache}`
      : '';
    const channelId = prioritisation
      ? `&channelId=${prioritisation.channelId}`
      : '';
    const month = prioritisation ? `&month=${prioritisation.month}` : '';
    const year = prioritisation ? `&year=${prioritisation.year}` : '';
    let queryString = `${pagination}${title}${titlesCount}${sort}${searchKey}${useCache}${channelId}${month}${year}`;

    if (queryString.includes('+')) {
      queryString = queryString.replace('+', '%2B');
    }

    return queryString;
  };

  const mapAvailableData = (data) => {
    let dataSelected = allGroups.filter(
      (x) => data.filter((y) => y.id === x.id).length
    );
    const availableData = dataSelected.map((group) => ({
      id: group.id,
      name: group.name,
      totalAsset: group.totalAsset,
      action: group.action,
    }));
    return availableData;
  };

  const mapPrioritiseData = (data) => {
    let dataSelected = allGroups.filter(
      (x) => data.filter((y) => y.id === x.id).length
    );
    const prioritiseData = dataSelected.map((group) => ({
      id: group.id,
      name: group.name,
      action: group.action,
    }));
    return prioritiseData;
  };

  const handleGetTitles = (initial) => {
    setIsLoading(true);
    PrioritisationService.getTitleGroupList(
      constructQueryParameters(queryParameters),
      initial
    )
      .then((response) => {
        const reponseData = response.data.data;
        mapAllData(reponseData, response.data.prioritised);
        let groupData = reponseData.map((group) => ({
          id: group.id,
          name: group.name,
          totalAsset: group.totalAsset,
          action: {
            id: group.id,
            icon: eyeIcon,
            toolTip: 'View title group details',
            actionName: 'viewTitleDetails',
          },
        }));

        if (!initial) {
          groupData = groupData.filter(
            (x) => !prioritisedGroups.filter((y) => y.id === x.id).length
          );
        }
        sortAvailable(groupData);

        if (initial) {
          const prioritisedData = response.data.prioritised.map((group) => ({
            id: group.id,
            name: group.name,
            action: {
              id: group.id,
              icon: eyeIcon,
              toolTip: 'View title group details',
              actionName: 'viewTitleDetails',
            },
          }));
          sortPrioritise(prioritisedData);
        }
      })
      .finally(() => setIsLoading(false));
  };

  const mapAllData = (availableData, prioritisedData) => {
    let allData = availableData.map((group) => ({
      id: group.id,
      name: group.name,
      totalAsset: group.totalAsset,
      action: {
        id: group.id,
        icon: eyeIcon,
        toolTip: 'View title group details',
        actionName: 'viewTitleDetails',
      },
    }));
    const priorityData = prioritisedData.map((group) => ({
      id: group.id,
      name: group.name,
      totalAsset: group.totalAsset,
      action: {
        id: group.id,
        icon: eyeIcon,
        toolTip: 'View title group details',
        actionName: 'viewTitleDetails',
      },
    }));
    allData = allData.concat(priorityData);
    setAllGroups(allData);
  };

  const selectTitleGroups = (e) => {
    setSelectedGroups(
      e.filter((x) => groups.filter((y) => y.id === x.id).length)
    );
  };

  const selectPrioritisedGroups = (e) => {
    setSelectedPrioritisedGroups(e);
  };

  const handleAddProgram = (e) => {
    const toPrioritise = mapPrioritiseData(selectedGroups);
    var prioritisedFilter = allPrioritisedGroups.concat(toPrioritise);
    sortPrioritise(prioritisedFilter);

    //set available data
    const filterGroups = allAvailableGroups.filter(
      (x) => !selectedGroups.filter((y) => y.id === x.id).length
    );
    sortAvailable(filterGroups);
    setSelectedGroups([]);
    setClearSelectedGroups(true);
    setChanged(true);
  };

  const handleRemoveAll = () => {
    //set available data
    const toAvailableData = mapAvailableData(allPrioritisedGroups);
    const filterGroups = allAvailableGroups.concat(toAvailableData);
    sortAvailable(filterGroups);
    setSelectedGroups([]);
    sortPrioritise([]);
    setClearSelectedPrioritise(true);
    setChanged(true);
  };

  const handleRemoveProgram = () => {
    //set available data
    const toAvailableData = mapAvailableData(selectedPrioritisedGroups);
    const filterGroups = allAvailableGroups.concat(toAvailableData);
    sortAvailable(filterGroups);
    setSelectedGroups([]);
    const filterPrioritise = allPrioritisedGroups.filter(
      (x) => !selectedPrioritisedGroups.filter((y) => y.id === x.id).length
    );
    sortPrioritise(filterPrioritise);
    setSelectedPrioritisedGroups([]);
    setClearSelectedPrioritise(true);
    setChanged(true);
  };

  const handleSelectDetail = (selectedId) => {
    var selected = allGroups.filter((y) => y.id === selectedId)[0];

    if (viewGroupDetails && selectedId === viewGroupDetails.id) {
      setViewGroupDetails();
      setSelectedViewId();
    } else {
      setViewGroupDetails(selected);
      setSelectedViewId(selected?.id);
    }
  };

  const callbackCloseDetails = () => {
    setViewGroupDetails();
    setSelectedViewId();
  };

  const handleSavePriotised = () => {
    let valid = true;
    const titleGroupIDs = [];
    setIsSaving(true);

    allPrioritisedGroups.map((value) => {
      titleGroupIDs.push(value.id);
      return true;
    });

    if (valid) {
      const prioritisationParameters = {
        titleGroupIDs: titleGroupIDs,
        channelId: prioritisation.channelId,
        month: prioritisation.month,
        year: prioritisation.year,
      };

      PrioritisationService.saveTitleGroupPriotisation(
        prioritisationParameters
      ).then(() => {
        setChanged(false);
        setIsSaving(false);
        success('Prioritisation Saved');
      });
    } else {
      setIsSaving(false);
    }
  };

  const sortAvailable = (list) => {
    var availableList = list;
    if (sortParameters.order === 1) {
      if (sortParameters.sort === 1) {
        availableList = availableList.sort((a, b) => {
          if (a.name.toLowerCase() > b.name.toLowerCase()) return 1;
          return -1;
        });
      } else {
        availableList = availableList.sort((a, b) => {
          if (a.totalAsset > b.totalAsset) return 1;
          return -1;
        });
      }
    } else {
      if (sortParameters.sort === 1) {
        availableList = availableList.sort((a, b) => {
          if (a.name.toLowerCase() > b.name.toLowerCase()) return -1;
          return 1;
        });
      } else {
        availableList = availableList.sort((a, b) => {
          if (a.totalAsset > b.totalAsset) return -1;
          return 1;
        });
      }
    }
    setAllAvailableGroups(availableList);
    let pageNumber =
      availableList.length > 0 && queryParameters.pageNumber === 0
        ? 1
        : queryParameters.pageNumber;
    let { pageSize } = queryParameters;
    let toSkip = pageNumber === 1 ? 0 : (pageNumber - 1) * pageSize;
    while (toSkip >= availableList.length) {
      pageNumber -= 1;
      toSkip = pageNumber === 1 ? 0 : (pageNumber - 1) * pageSize;
    }
    const availableGroupData = availableList.slice(toSkip, toSkip + pageSize);
    setGroups(availableGroupData);

    setPaginationParameters((prevState) => ({
      ...prevState,
      currentPage: pageNumber,
      totalCount: availableList.length,
      totalPages: Math.ceil(
        availableList.length / paginationParameters.pageSize
      ),
    }));
  };

  const sortPrioritise = (list) => {
    var prioritisedList = list;
    if (sortPrioritiseParameters.order === 1) {
      prioritisedList = prioritisedList.sort((a, b) => {
        if (a.name.toLowerCase() > b.name.toLowerCase()) return 1;
        return -1;
      });
    } else {
      prioritisedList = prioritisedList.sort((a, b) => {
        if (a.name.toLowerCase() > b.name.toLowerCase()) return -1;
        return 1;
      });
    }
    setAllPrioritisedGroups(prioritisedList);
    let pageNumber =
      prioritisedList.length > 0 && prioritisedQueryParameters.pageNumber === 0
        ? 1
        : prioritisedQueryParameters.pageNumber;
    let { pageSize } = prioritisedQueryParameters;
    let toSkip = pageNumber === 1 ? 0 : (pageNumber - 1) * pageSize;
    while (toSkip >= prioritisedList.length) {
      pageNumber -= 1;
      toSkip = pageNumber === 1 ? 0 : (pageNumber - 1) * pageSize;
    }
    const prioritisedGroupData = prioritisedList.slice(
      toSkip,
      toSkip + pageSize
    );
    setPrioritisedGroups(prioritisedGroupData);

    setPaginationParametersPrioritised((prevState) => ({
      ...prevState,
      currentPage: pageNumber,
      totalCount: prioritisedList.length,
      totalPages: Math.ceil(
        prioritisedList.length / paginationParametersPrioritised.pageSize
      ),
    }));
  };

  useEffect(() => {
    sortPrioritise(allPrioritisedGroups);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [sortPrioritiseParameters, prioritisedQueryParameters]);

  useEffect(() => {
    sortAvailable(groups);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [sortParameters]);

  useEffect(() => {
    if(prioritisation.channelId && prioritisation.month && prioritisation.year){
      handleGetTitles(true);
      setViewGroupDetails();
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [prioritisation]);

  useEffect(() => {
    if (discardTitleGroupDirty) {
      setChanged(false);
      setDiscardTitleGroupDirty(false);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [discardTitleGroupDirty]);

  useEffect(() => {
    if (saveTitleGroupDirty) {
      handleSavePriotised();
      setChanged(false);
      setSaveTitleGroupDirty(false);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [saveTitleGroupDirty]);

  useEffect(() => {
    setAllGroups((old) =>
      old.map((item) => ({
        ...item,
        action: { ...item.action, icon: item.id === selectedViewId ? eyeSlashIcon : eyeIcon },
      }))
    );
    setGroups((old) =>
      old.map((item) => ({
        ...item,
        action: { ...item.action, icon: item.id === selectedViewId ? eyeSlashIcon : eyeIcon },
      }))
    );
    setPrioritisedGroups((old) =>
      old.map((item) => ({
        ...item,
        action: { ...item.action, icon: item.id === selectedViewId ? eyeSlashIcon : eyeIcon },
      }))
    );
  }, [allAvailableGroups, allGroups, allPrioritisedGroups, selectedViewId]);

  return (
    <>
      <div className="d-flex">
        <div style={{ width: '60%' }}>
          <AvailableTitleGroupComponent
            selectTitleGroups={selectTitleGroups}
            groups={groups}
            setGroups={setGroups}
            paginationParameters={paginationParameters}
            setPaginationParameters={(e) => setPaginationParameters(e)}
            clearSelected={clearSelectedGroups}
            setClearSelected={setClearSelectedGroups}
            selectDetail={handleSelectDetail}
            tableHeight={viewGroupDetails ? '683px' : '467px'}
            setPrioritisedGroups={(e) => {
              setPrioritisedGroups(e);
            }}
            prioritisation={prioritisation}
            handleGetTitles={handleGetTitles}
            isLoading={isLoading}
            queryParameters={queryParameters}
            setQueryParameters={setQueryParameters}
            sortParameters={sortParameters}
            setSortParameters={setSortParameters}
          />
        </div>
        <div className={Styles.divButtons}>
          {!isLoading && (
            <>
              <button
                onClick={handleAddProgram}
                disabled={selectedGroups.length === 0}
                type="button"
              >
                Add Selected
                <img style={{ marginTop: '11px' }} src={arrowLeftIcon} alt="" />
              </button>
              <button
                onClick={handleRemoveAll}
                style={{ paddingTop: '9px' }}
                disabled={prioritisedGroups.length === 0}
                type="button"
              >
                <img style={{ marginTop: '2px' }} src={arrowRightIcon} alt="" />
                <span style={{ marginLeft: '5px' }}>Remove All</span>
              </button>
              <button
                onClick={handleRemoveProgram}
                disabled={selectedPrioritisedGroups.length === 0}
                type="button"
              >
                <img
                  style={{ marginTop: '11px' }}
                  src={arrowRightIcon}
                  alt=""
                />
                Remove Selected
              </button>
            </>
          )}
        </div>

        <div style={{ width: '33%' }}>
          {prioritisedGroups.length === 0 ? (
            <div
              className={viewGroupDetails
                ? Styles.divNoAvailableChannelSelectedSmall
                : Styles.divNoAvailableChannelSelected
              }
            >
              <div
                style={{
                  display: 'table-cell',
                  verticalAlign: 'middle',
                }}
              >
                <div>
                  Please select a title group and add.
                </div>
              </div>
            </div>
          ) : (
            <PrioritisedTitleGroupComponent
              prioritisedGroups={prioritisedGroups}
              selectPrioritisedGroups={selectPrioritisedGroups}
              paginationParameters={paginationParametersPrioritised}
              setPaginationParameters={(e) =>
                setPaginationParametersPrioritised(e)
              }
              selectDetail={handleSelectDetail}
              tableHeight={viewGroupDetails ? '684px' : '468px'}
              isLoading={isLoading}
              sortParameters={sortPrioritiseParameters}
              setSortParameters={(e) => {
                setSortPrioritiseParameters(e);
              }}
              clearSelected={clearSelectedPrioritise}
              setClearSelected={setClearSelectedPrioritise}
              queryParameters={prioritisedQueryParameters}
              setQueryParameters={setPrioritisedQueryParameters}
            />
          )}
        </div>
      </div>
      {viewGroupDetails && (
        <TitleGroupDetailsComponent
          viewGroupDetails={viewGroupDetails}
          callbackCloseDetails={callbackCloseDetails}
          prioritisation={prioritisation}
        />
      )}
      <div style={{ float: 'right', marginTop: '8px' }}>
        <Button
          text="Save"
          isLoading={isSaving}
          onClick={handleSavePriotised}
          disabled={!changed}
        />
      </div>
    </>
  );
}
